
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import FooterNav from '@/components/common/FooterNav'
export default {
data () {
return {
stateNum: this.$route.query.index
}
},
computed: {
orderState () {
console.log(this.stateNum)
if(this.$route.query.orderState.length)
return this.$route.query.orderState[this.stateNum]
else
return this.$route.query.orderState
},
showIt () {
return this.orderState.orders == ''
}
},
components: {
XButton,
Cell,
FooterNav
},
beforeMount () {

},
mounted () {
if (this.orderState.title === '') {
document.title = this.orderState.title
} else {
document.title = this.orderState.title + ''
}
},
methods: {
goShop () {
console.log('')
}
}
}



