
import PaletteButton from '@/components/common/palette-button'
import FooterNav from '@/components/common/FooterNav'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
orderState: this.$route.query.orderState,
showIt: true,
orders: [],
show1: true
}
},
computed: {
shopInfo: function () {
return JSON.parse(window.sessionStorage.getItem('shopInfo'))
},
curOrders: function () {
let self = this
let result
if (self.orderState === '' || !self.orderState) {
result = self.orders
} else {
if (self.orders) {
result = self.orders.filter(function (list) {
return list.status === self.orderState
})
} else {
result = []
}
}
console.log(result)
if (!!result && result.length > 0) {
self.showIt = true
} else {
self.showIt = false
}
return result
}
},
components: {
XButton,
FooterNav,
PaletteButton,
Cell,
Loading,
Divider
},
created () {
this.getCouponList()
},
mounted () {
if (this.$route.query.orderState === '' || !this.$route.query.orderState) {
document.title = ''
} else {
document.title = this.$route.query.orderState + ''
}
},
methods: {
getCouponList () {
console.log('145')
this.showLoading()
console.log(this.$route.query.orderState)
let self = this
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_ORDER_LIST), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
let result = res.body.data
console.log(result)
self.orders = result
if (!self.orders) {
return
}
if (!!self.orders && self.orders.length > 0) {
for (var i = self.orders.length - 1; i >= 0; i--) {
let endTime = parseInt(self.orders[i].createTime + self.orders[i].expireIn * 60000, 10)
self.orders[i].endTime = self.formatDate(endTime, 'yyyy-MM-dd HH:mm:ss')
self.orders[i].icon = self.getImageView(3, 170, 93, self.orders[i].icon)
if (self.orders[i].status === 'PAY_FAIL') {
self.orders[i].status = ''
} else if (self.orders[i].status === 'SUCCESS_FULL' || self.orders[i].status === 'SUCCESS_DEPOSIT') {
self.orders[i].status = ''
} else if (self.orders[i].status === 'FINISH') {

if (self.orders[i].evaluated === 0) {
self.orders[i].status = ''
} else {
self.orders[i].status = ''
}
} else {
self.orders[i].status = ''
}
if (self.orders[i].payStyle === 'FULL') {
self.orders[i].payStyle = ''
} else {
self.orders[i].payStyle = ''
}
}
}
}
})
},
viewOrderDetail (list) {
this.$router.push({name: 'orderDetail', query: {orderNum: list.orderNum}})
},
payLabel (sta) {
let str
if (sta === '' || sta === '') {
str = ''
} else {
str = ''
}
return str
},
cancelBtn (sta) {
let str
if (sta === '') {
str = ''
} else if (sta === '') {
str = ''
} else {
str = ''
}
return str
},
confirmBtn (sta) {
let str = ''
if (sta === '') {
str = ''

} else if (sta === '') {
str = ''

} else if (sta === '') {
str = ''

} else if (sta === '') {
str = ''

}
return str
},
cancelAction (list, index) {
let self = this
if (list.status === '') {
self.$vux.confirm.show({
title: '',
onConfirm () {
self.updateOrderStatus(list, 'DELETED')
self.orders.splice(index, 1)
console.log(self.orders)
},
onCancel () {
console.log('onCancel')
}
})
} else if (list.status === '') {
self.$vux.confirm.show({
title: '',
onConfirm () {
self.updateOrderStatus(list, 'CLOSED')
list.status = ''
console.log(self.orders)
},
onCancel () {
console.log('onCancel')
}
})
} else {



}
},
confirmAction (list) {
if (list.status === '') {
this.$router.push({name: 'goodsDetail', query: {mallGoodsCode: list.mallGoodsCode}})
} else if (list.status === '') {

window.location.href = 'tel://' + list.shopTel
} else if (list.status === '') {
let WxPayUnifiedOrderRequest = {
'appId': this.getBaseParam(cookie).appId,
'shopCode': this.getBaseParam(cookie).shopCode,
'attach': list.mallGoodsCode,
'body': list.title,
'outTradeNo': list.orderNum,
'totalFee': list.shouldPay
}
this.$router.push({path: '/pay/weitallPay', query: {endTime: list.endTime, WxPayUnifiedOrderRequest: JSON.stringify(WxPayUnifiedOrderRequest)}})
} else if (list.status === '') {
this.$router.push({name: 'orderDetail', query: {orderNum: list.orderNum}})
}
},
showLoading () {
setTimeout(() => {

this.show1 = false
}, 1000)
},
contactUs () {
window.location.href = 'tel://' + JSON.parse(window.sessionStorage.getItem('shopInfo')).telephone
}
}
}



