
import PaletteButton from '@/components/common/palette-button'
import FooterNav from '@/components/common/FooterNav'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Popover from 'vux/src/components/popover/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
signed: 0,
showSignIn: false,
signInInfo: {},
gameList: [],
couponList: []
}
},
computed: {
userScore: function () {
return this.$store.state.userScore
},
signStatus: function () {
let self = this
let res
if (self.signed === 1) {
res = ''
} else {
res = ''
}
return res
}
},
components: {
PaletteButton,
FooterNav,
XButton,
Swiper,
SwiperItem,
Divider,
XDialog,
Popover
},




created () {
var self = this
self.fetchData()
},
methods: {
fetchData () {
let self = this
let baseParam = self.getBaseParam(cookie)
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_MAIN_PAGEDATA), {appId: baseParam.appId, shopCode: baseParam.shopCode}).then((res) => {
if (res.body.code === 1) {
let scorePageData = res.body.data
if (!scorePageData.areaD || !scorePageData.areaE) {
self.$vux.toast.text('', 'middle')
self.$router.go(-1)
return
}
let tmpScore = {}
console.log(scorePageData)
if (scorePageData.areaA) {
console.log('ohk')
tmpScore = scorePageData.areaA
} else {
tmpScore.score = 0
tmpScore.frozenScore = 0
console.log('none')
}
console.log(JSON.stringify(tmpScore))
self.$store.dispatch('setUserScore', JSON.stringify(tmpScore))
self.signed = scorePageData.signed
self.couponList = scorePageData.areaE
console.log(self.couponList)
for (let i = self.couponList.length - 1; i >= 0; i--) {
self.couponList[i].bgMode = Math.round(Math.random() + 1)
}
if (!scorePageData.areaD) {
console.log('')
return
} else {
for (let i = 0; i < scorePageData.areaD.length; i++) {
scorePageData.areaD[i].img = self.getImageView(3, 640, 340, scorePageData.areaD[i].img)
}
self.gameList = scorePageData.areaD

}
} else {
self.$vux.toast.text('', 'middle')
self.$router.go(-1)
}
})
},
goSignIn () {
let self = this
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_SIGN_IN), self.setBaseForm(cookie)).then((res) => {
console.log(res)
if (res.body.code === 1) {
let result = res.body.data
self.userScore.score += result.score
self.$store.dispatch('setUserScore', JSON.stringify(self.userScore))
self.showSignIn = true
self.signed = 1
self.signInInfo.continueDays = result.continueDays
self.signInInfo.score = result.score
console.log(self.signInInfo)
setTimeout(() => {
this.showSignIn = false
}, 3000)
} else {
self.$vux.toast.text(res.body.message, 'middle')
}
})
},
exchangeCoupon (list) {
let self = this
if (list.left > 0) {
let requestHead = self.setBaseForm(cookie)
requestHead.shopCouponCode = list.code
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_COUPON_EXCHANGE), requestHead).then((res) => {
console.log(res)
if (res.body.code === 1) {
let result = res.body.message
self.userScore.score -= list.score
self.$store.dispatch('setUserScore', JSON.stringify(self.userScore))
self.$vux.toast.text('', 'middle')
console.log(result)
} else {
self.$vux.toast.text(res.body.message, 'middle')
}
})
} else {
self.$vux.toast.text('', 'middle')
}
},
contactUs () {
window.location.href = 'tel://' + JSON.parse(window.sessionStorage.getItem('shopInfo')).telephone
}
}
}



