console.log('StrechBarrageStart')
let self = this
function StrechBarrage (item) {
  this.item = item
  /*  */
  this.item.duration = 10000
  /*  */
  this.item.size = '16px'
  /*  */
  this.item.color = '#FFF'
  /*  */
  this.appearanceStretch = this.makeUp()
  /*  */
  this.appearanceStretch.hide().appendTo($('#tals-stretch-barrage'))
}
//  
StrechBarrage.prototype.makeUp = function () {
  let appearanceStretch = $('<div><img src="" alt="" class="fromAvatar" /><p class="stretch-barrage-info">' + this.item.nickname + ' <br /><span>' + this.item.spName + '</span></p>' + '<div class="stretch-barrage-gift"><img src="" alt="" class="stretch-scale"/>X<span class="stretch-scale">' + this.item.quantity + '</span></div>' + '</div>')
  // console.log('')
  // console.log(this.item.id)
  appearanceStretch.css({
    'position': 'relative',
    'width': '280px',
    'height': '40px',
    'border-radius': '40px',
    'text-align': 'left',
    'padding-left': '10px',
    'padding-right': '10px',
    'font-family': 'PingFangSC-Medium',
    'background': '#E9551F'
  })
  appearanceStretch.find('.fromAvatar').attr('src', this.item.userAvatar).empty().css({
    'display': 'inline-block',
    'position': 'absolute',
    'top': '-1px',
    'left': '-2px',
    'width': '42px',
    'height': '42px',
    'border-radius': '42px'
  })
  appearanceStretch.find('.stretch-barrage-info').css({
    'position': 'absolute',
    'left': '45px',
    'top': '2px',
    'width': '120px',
    'height': '100%',
    'display': 'inline-block',
    'font-size': '15px',
    'color': this.item.color,
    'letter-spacing': '-0.29px',
    'white-space': 'nowrap',
    'text-overflow': 'ellipsis',
  })
  appearanceStretch.find('.stretch-barrage-info>span').css({
    'position': 'absolute',
    'bottom': '2px',
    'font-size': '12px',
    'color': '#DFCCC5'
  })
  appearanceStretch.find('.stretch-barrage-gift').css({
    'position': 'absolute',
    'right': 0,
    'top': '-25px',
    'width': '90px',
    'height': '50px',
    'color': '#F8E92E',
    'line-height': '50px'
  })
  appearanceStretch.find('.stretch-barrage-gift>span').css({
    'display': 'inline-block',
    'font-size': '32px',
    'margin-left': '5px',
    'letter-spacing': '-0.45px'
  })
  appearanceStretch.find('.stretch-barrage-gift>img').attr('src', this.item.spSecondKey).css({
    'width': '40px',
    'height': '40px'
  })
  return appearanceStretch
}
//  
StrechBarrage.prototype.animate = function () {
  let that = this
  let appearanceStretch = that.appearanceStretch
  let mywidth = appearanceStretch.width()
  // console.log('animate')
  // left
  appearanceStretch.animate({
    left: '10px'
  }, 800, 'swing', function () {
    setTimeout(function () {
      $('.stretch-scale').addClass('animated pulse')
    }, 1000)
    setTimeout(function () {
      appearanceStretch.animate({
        left: -mywidth,
      }, 500, 'swing', function () {
        appearanceStretch.hide()
      })
    }, 2000)
  })
}
//  
StrechBarrage.prototype.perform = function () {
  let that = this
  let appearanceStretch = that.appearanceStretch
  let mywidth = appearanceStretch.width()
  // ,
  appearanceStretch.css({
    position: 'absolute',
    left: '10px',
    zIndex: 1,
    display: 'block'
  })
  //  
  //  
  //  
  that.animate()
  // console.log(that.item.time)
  // let delayTime = that.item.time - (that.item.session - 1) * self.CheckTime
  // setTimeout(function () {
  //   that.animate()
  // }, delayTime)
}

module.exports = StrechBarrage



