/**
* sessionStorage
* @param key
* @param raw true:JSON
* @returns {null}
*/
Vue.prototype.getSessionStorageItem = function (key, raw) {
let tmpVal = window.sessionStorage.getItem(key)
if (!tmpVal) return null
return raw ? JSON.parse(tmpVal) : tmpVal
}
/**
*
* @param key
* @param val
* @param raw true:JSON
*/
Vue.prototype.setSessionStorageItem = function (key, val, raw) {
if (raw) {
val = JSON.stringify(val)
}
window.sessionStorage.setItem(key, val)
}

Vue.prototype.setBaseForm = function (cookie) {
let self = this
let baseParam = self.getBaseParam(cookie)
return {
appId: baseParam.appId,
shopCode: baseParam.shopCode
}
}

/**
* 
* @param cookie
* @returns {{appId, shopCode, userToken, source}}
*/
Vue.prototype.getBaseParam = function (cookie) {
let self = this

let tmpAppId = self.getSessionStorageItem(self.WtConst.FILED_APPID, false)
let tmpShopCode = self.getSessionStorageItem(self.WtConst.FILED_SHOP_CODE, false)
let tmpUserToken = self.getSessionStorageItem(self.WtConst.FILED_USER_TOKEN, false)






if (self.isEmpty(tmpAppId) || self.isEmpty(tmpShopCode) || self.isEmpty(tmpUserToken)) {

tmpAppId = cookie.get(self.WtConst.FILED_APPID)
tmpShopCode = cookie.get(self.WtConst.FILED_SHOP_CODE)
tmpUserToken = cookie.get(self.WtConst.FILED_USER_TOKEN)

if (!tmpAppId) tmpAppId = self.getUrlParam(self.WtConst.FILED_APPID)
if (!tmpShopCode) tmpShopCode = self.getUrlParam(self.WtConst.FILED_SHOP_CODE)
if (!tmpUserToken) tmpUserToken = self.getUrlParam(self.WtConst.FILED_USER_TOKEN)
if (!tmpAppId || !tmpShopCode) {


}

}
self.setSessionStorageItem(self.WtConst.FILED_APPID, tmpAppId, false)
self.setSessionStorageItem(self.WtConst.FILED_SHOP_CODE, tmpShopCode, false)
self.setSessionStorageItem(self.WtConst.FILED_USER_TOKEN, tmpUserToken, false)
return {
appId: tmpAppId,
shopCode: tmpShopCode
}
}

Vue.prototype.getPageStructureInfo = function () {

}

Vue.prototype.getBaseShopInfoFromSessionStorage = function () {
let self = this
return {
shopInfo: self.getSessionStorageItem('shopInfo', true),
mainModuleList: self.getSessionStorageItem('mainModuleList', true),
navModuleList: self.getSessionStorageItem('navModuleList', true)
}
}

/**
* 
* @param data
*/
Vue.prototype.formatBaseShopInfo = function (data) {
let self = this
let shopInfo = {}
let mainModuleList = []
let navModuleList = []
if (!data) {
return
}

if (!!data.shopInfo) {
shopInfo = data.shopInfo
shopInfo.qrcode = !!shopInfo.qrcode ? self.getImageView(3, 258, 258, shopInfo.qrcode) : ''
shopInfo.icon = !!shopInfo.icon ? self.getImageView(3, 100, 100, shopInfo.icon) : ''
shopInfo.mainImg = !!shopInfo.mainImg ? self.getImageView(3, 640, 493, shopInfo.mainImg) : ''
if (!!shopInfo.images && shopInfo.images.length > 0) {
for (let i = 0; i < shopInfo.images.length; i++) {
shopInfo.images[i] = self.getImageView(3, 640, 474, shopInfo.images[i])
}
}
shopInfo.titleImgNew = !!shopInfo.titleImgNew ? self.getImageView(3, 640, 208, shopInfo.titleImgNew) : ''
shopInfo.titleImgHot = !!shopInfo.titleImgHot ? self.getImageView(3, 640, 208, shopInfo.titleImgHot) : ''
shopInfo.separatorImg = !!shopInfo.separatorImg ? self.getImageView(3, 800, 46, shopInfo.separatorImg) : ''
if (!!shopInfo.introductionImgs && shopInfo.introductionImgs.length > 0) {
for (let i = 0; i < shopInfo.introductionImgs.length; i++) {
shopInfo.introductionImgs[i] = self.getImageView(3, 640, 724, shopInfo.introductionImgs[i])
}
}

}

if (!!data.mainModuleList) {
mainModuleList = data.mainModuleList
for (let i=0; i<mainModuleList.length; i++) {
mainModuleList[i].styleValue = !!mainModuleList[i].styleValue ? self.getImageView(3, 400, 400, mainModuleList[i].styleValue) : ''
}

}


if (!!data.navModuleList) {
navModuleList = data.navModuleList
for (let i=0; i<navModuleList.length; i++) {
navModuleList[i].styleValue = !!navModuleList[i].styleValue ? self.getImageView(3, 100, 100, navModuleList[i].styleValue) : ''
}
}
self.setSessionStorageItem('shopInfo', shopInfo, true)
self.setSessionStorageItem('mainModuleList', mainModuleList, true)
self.setSessionStorageItem('navModuleList', navModuleList, true)
return {
shopInfo: shopInfo,
mainModuleList: mainModuleList,
navModuleList: navModuleList
}
}

/**
* 
*  signatureAPI
* @param wxOperateForm
*/
Vue.prototype.setWxOperate = function (wxOperateForm, reportRequest, bizParams) {
let self = this
if (!self.isWechatBrowser()) {


return
}
self.$http.post(self.addAPIVersion(self.WtConst.API_SIGNATURE_JSAPI_GET), wxOperateForm).then((res) => {
if (res.body.code === 1) {
let signature = res.body.data
if (!signature) {

return
}
let jsApiList = signature.jsApiList;
let wxOperateList = signature.wxOperateList;
if (!jsApiList || jsApiList.length <= 0 || !wxOperateList || wxOperateList.length <= 0) {

return
}

self.$wechat.config({
debug: false,
appId: wxOperateForm.appId,
timestamp: signature.timest,
nonceStr: signature.nonceStr,
signature: signature.signature,
jsApiList: jsApiList
})
self.$wechat.ready(function () {
for (let i = 0; i < wxOperateList.length; i++) {
let wxOperate = wxOperateList[i]
let jsApi = wxOperate.jsApi
let jsApiContent = wxOperate.jsApiContent
if ('onMenuShareAppMessage' === jsApi) {


let pageUrl = jsApiContent.link;

if(!self.isEmpty(bizParams)) {
if (pageUrl.indexOf('?') >= 0) {
pageUrl += ('&r=' + bizParams.r + '&p=' + bizParams.p);
} else {
pageUrl += ('?r=' + bizParams.r + '&p=' + bizParams.p);
}
}

self.$wechat.onMenuShareAppMessage({
title: jsApiContent.title,
desc: jsApiContent.desc,
link: pageUrl,
imgUrl: self.getImageView(3, 100, 100, jsApiContent.imgUrl),
trigger: function (res) {


},
success: function (res) {
if (!self.isEmpty(reportRequest)) {
reportRequest.operate = 'appMessage'
self.$http.post(self.addAPIVersion(self.WtConst.API_DATA_COLLECTION_WXSHARE_REPORT), reportRequest).then((reportRes) => {
if (reportRes.body.code === 1) {

}
})
}


},
cancel: function (res) {

},
fail: function (res) {

}
})
} else if ('onMenuShareTimeline' === jsApi) {


let pageUrl = jsApiContent.link;
if(!self.isEmpty(bizParams)) {
if (pageUrl.indexOf('?') >= 0) {
pageUrl += ('&r=' + bizParams.r + '&p=' + bizParams.p);
} else {
pageUrl += ('?r=' + bizParams.r + '&p=' + bizParams.p);
}
}

self.$wechat.onMenuShareTimeline({
title: jsApiContent.title,
desc: jsApiContent.desc,
link: pageUrl,
imgUrl: self.getImageView(3, 100, 100, jsApiContent.imgUrl),
trigger: function (res) {


},
success: function (res) {
if (!self.isEmpty(reportRequest)) {
reportRequest.operate = 'timeline'
self.$http.post(self.addAPIVersion(self.WtConst.API_DATA_COLLECTION_WXSHARE_REPORT), reportRequest).then((reportRes) => {
if (reportRes.body.code === 1) {

}
})
}                    // TODO: 

},
cancel: function (res) {

},
fail: function (res) {

}
})
} else if ('getLocation' === jsApi) {

self.$wechat.getLocation({
type: 'wgs84', // wgs84gpsopenLocation'gcj02'
success: function (res) {
let DataLocationReportRequest = {
shopCode: wxOperateForm.shopCode,
pageCode: wxOperateForm.pageCode,
latitude: res.latitude, // 90 ~ -90
longitude: res.longitude, // 180 ~ -180
speed: res.speed, // /
accuracy: res.accuracy // 
}

self.$http.post(self.addAPIVersion(self.WtConst.API_DATA_COLLECTION_LOCATION_REPORT), DataLocationReportRequest).then((res) => {

})
}
});
} else if ('openLocation' === jsApi) {









} else if ('hideMenuItems' === jsApi) {
self.$wechat.hideMenuItems({
menuList: jsApiContent,
success: function (res) {

},
fail: function (res) {

}
})
}
}
})
self.$wechat.error(function (res) {

})
} else {

}
})
}

