
import XProgress from 'vux/src/components/x-progress/index.vue'

export default {
props: {
compData: Array,
collectNum: Number
},
data () {
return {
commodityList: []
}
},
components: {
XProgress
},
methods: {





unlockState (index) {
let self = this
let res
if (self.compData[index].condition <= self.collectNum) {
res = false
} else {
res = true
}
return res
}
}
}



