  /* eslint-disable semi,fun-call-spacing,space-before-function-paren */
import FooterNav from '@/components/common/FooterNav'
import Cell from 'vux/src/components/cell/index.vue';
import Group from 'vux/src/components/group/index.vue';
export default {
  data () {
    return {
      userInfo: {}
    }
  },
  components: {
    FooterNav,
    Group,
    Cell
  },
  mounted () {
    let self = this
    if (JSON.parse(window.sessionStorage.getItem('userInfo'))) {
      self.userInfo = JSON.parse(window.sessionStorage.getItem('userInfo'))
      if (self.userInfo.sex === 1) {
        self.userInfo.sex = ''
      } else if (self.userInfo.sex === 2) {
        self.userInfo.sex = ''
      } else {
        self.userInfo.sex = ''
      }
    }
  }
}



