
import NavHeaderAA from '@/marketing/common/NavHeaderAA'
import BannerAA from '@/marketing/common/BannerAA'
import SeparatorAA from '@/marketing/common/SeparatorAA'
import CommodityDisplayAA from '@/marketing/common/CommodityDisplayAA'
import TextAA from '@/marketing/common/TextAA'
import AdvertisementAA from '@/marketing/common/AdvertisementAA'
import MarketingQrcodeAssist from '@/marketing/activity/qrcode/MarketingQrcodeAssist'
import MarketingQrcodeSupport from '@/marketing/activity/qrcode/MarketingQrcodeSupport'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
computed: {
pageCode () {
return this.$route.params.pageCode
}
},
data () {
return {
title: '',
compList: []
}
},
components: {
NavHeaderAA,
BannerAA,
SeparatorAA,
CommodityDisplayAA,
TextAA,
AdvertisementAA,
MarketingQrcodeAssist,
MarketingQrcodeSupport
},
beforeMount () {
let self = this
self.initPageInfo()
},
mounted () {

let baseParam = this.getBaseParam(cookie)
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode,
href: window.location.href
}
this.setWxOperate(wxOperateForm)
},
methods: {
initPageInfo () {

let self = this
let baseParam = self.getBaseParam(cookie)
let qrCodeParam = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode
}

self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_QRCODE_PAGEDATA), qrCodeParam).then((res) => {

if (res.body.code === 1) {
let result = res.body.data
console.log(result)
self.compList = result.compDataList
document.title = result.pageTitle
}
})
}
}
}



