
/* eslint-disable indent,no-extra-boolean-cast,no-underscore-dangle,padded-blocks,space-before-function-paren,fun-call-spacing,no-console,space-infix-ops */
import _ from 'lodash'
import Divider from 'vux/src/components/divider/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
listChoiceness: [],
orderByKey: 'soldNum',
sortOrders: 'desc',
catagory: this.$route.query.catagory
}
},
components: {
Divider
},
created () {
var self = this
self.fetchData()
},
computed: {
ChoicenessComputed: function () {
let self = this
return _.orderBy(this.listChoiceness, self.orderByKey, self.sortOrders)
}
},
methods: {
fetchData () {
let self = this
let requestHead = self.setBaseForm(cookie)
requestHead.catagory = self.catagory
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_LIST), requestHead).then((res) => {
if (res.body.code === 1) {
self.listChoiceness = res.body.data.mallGoodsList
if (!self.listChoiceness) {
return
}
if (!!self.listChoiceness && self.listChoiceness.length > 0) {
for (var i = self.listChoiceness.length - 1; i >= 0; i--) {
self.listChoiceness[i].icon = self.getImageView(3, 185, 228, self.listChoiceness[i].icon)
}
}
console.log(self.listChoiceness)
}
})
},
isOrderKey (k, order) {
let elKs = document.getElementsByClassName('choiceNav')
let elK = document.getElementsByClassName(k)[0]
for (var i = elKs.length - 1; i >= 0; i--) {
elKs[i].style.color = 'black'
document.getElementsByClassName('icon-top')[0].style.color = 'black'
document.getElementsByClassName('icon-xia')[0].style.color = 'black'
}
elK.style.color = 'red'
this.orderByKey = k
this.sortOrders = order
document.documentElement.scrollTop = 0
document.body.scrollTop = 0
},
toggleOrderKey () {
let elKs = document.getElementsByClassName('choiceNav')
let elK = document.getElementsByClassName('preferentialPrice')[0]
for (var i = elKs.length - 1; i >= 0; i--) {
elKs[i].style.color = 'black'
}
elK.style.color = 'red'
this.orderByKey = 'preferentialPrice'
if (this.sortOrders !== 'asc') {
this.sortOrders = 'asc'
document.getElementsByClassName('icon-top')[0].style.color = 'red'
document.getElementsByClassName('icon-xia')[0].style.color = 'black'
} else {
this.sortOrders = 'desc'
document.getElementsByClassName('icon-xia')[0].style.color = 'red'
document.getElementsByClassName('icon-top')[0].style.color = 'black'
}
document.documentElement.scrollTop = 0
document.body.scrollTop = 0
}
}
}



