

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
directives: {
TransferDom
},
data () {
return {
realName: this.$route.query.realName,
mobile: this.$route.query.mobile,
goEditInfo: false,
curGoodsCode: this.$route.query.mallGoodsCode,
leaveMsg: '',
isFullPay: true,
isBack: false,
completeInfo: ''
}
},
computed: {
goodsInfo: function () {
let str
if (this.$route.query.goodsInfo) {

str = JSON.parse(this.$route.query.goodsInfo)
window.sessionStorage.setItem('goodsInfo', JSON.stringify(this.$route.query.goodsInfo))
} else {

str = JSON.parse(JSON.parse(window.sessionStorage.getItem('goodsInfo')))
console.log(str)
}
return str
},
shouldPay: function () {
if (this.isFullPay) {
return this.goodsInfo.preferentialPrice
} else {
return this.goodsInfo.deposit
}
},
oweMoney: function () {
if (this.isFullPay) {
return 0
} else {
return this.goodsInfo.preferentialPrice - this.goodsInfo.deposit
}
},
guestInfo: function () {
let res
if (!JSON.parse(window.sessionStorage.getItem('guestInfo'))) {
res = {
name: self.userName,
phone: self.userTel
}
} else {
res = JSON.parse(window.sessionStorage.getItem('guestInfo'))
}
if (res.name && res.phone) {
this.completeInfo = true
} else {
this.completeInfo = false
}
console.log(res.name)
return res
}
},
mounted () {
console.log('o1')
console.log(this.$route)
},
components: {
Group,
Cell,
CellFormPreview,
TransferDom,
Popup,
XInput,
XTextarea,
XButton
},
methods: {
log (str) {
console.log(str)
},
submitEdit () {
this.goEditInfo = false
console.log(this.guestInfo)
window.sessionStorage.setItem('guestInfo', JSON.stringify(this.guestInfo))
console.log(JSON.parse(window.sessionStorage.getItem('guestInfo')))
if (this.guestInfo.name && this.guestInfo.phone) {
this.completeInfo = true
} else {
this.completeInfo = false
}
},
changeTimeType (times) {
let endTimes = new Date(parseInt(times, 10))
let endyear = endTimes.getFullYear()
let endMonth = endTimes.getMonth() + 1
let endDay = endTimes.getDate()
let endHour = endTimes.getHours()
let endMin = endTimes.getMinutes()
let endSec = endTimes.getSeconds()
console.log(endyear, endMonth, endDay, endHour, endMin, endSec)
return endyear + '-' + endMonth + '-' + endDay + ' ' + endHour + ':' + endMin + ':' + endSec
},
pushOrder () {
let self = this
let baseParam = self.getBaseParam(cookie)
if (this.guestInfo.name && this.guestInfo.phone) {
self.$vux.confirm.show({
content: "<p style='text-align:left;width:100%;line-height:2em;' ><span style='display:inline-block;width:80px;text-align:justify;'>&nbsp;&nbsp;&nbsp;&nbsp&nbsp&nbsp&nbsp:</span><span style='font-size: 13px;color: #333333;padding-left:10px;'>" + this.guestInfo.name + '</span></p>' + "<p style='text-align:left;width:100%;line-height:2em;' ><span style='display:inline-block;width:80px;'>:</span><span style='font-size: 13px;color: #333333;padding-left:10px;'>" + this.guestInfo.phone + '</span></p>' + "<p style='text-align:left;width:100%;line-height:2em;' ><span style='display:inline-block;width:80px;'>:</span><span style='font-size: 13px;color: #333333;padding-left:10px;'>" + this.goodsInfo.shopName + '</span></p>',

confirmText: '',
cancelText: '',
onCancel () {
console.log('plugin onCancel')

},
onConfirm () {
let startT = new Date()
let xxStart = startT.getTime()
let yyEnd = startT.getTime() + 1000 * 60 * 15
let startTime = self.formatDate(xxStart, 'yyyy-MM-dd HH:mm:ss')
let endTime = self.formatDate(yyEnd, 'yyyy-MM-dd HH:mm:ss')
self.goodsInfo.startTime = startTime
self.goodsInfo.endTime = endTime
if (self.isFullPay) {
self.goodsInfo.payment = 'FULL'
} else {
self.goodsInfo.payment = 'DEPOSIT'
}
self.goodsInfo.shouldPay = self.shouldPay
console.log(self.oweMoney)
if (self.oweMoney) {
self.goodsInfo.oweMoney = self.oweMoney
} else {
self.goodsInfo.oweMoney = 0
}
let orderNum
if (self.goodsInfo.orderNum) {
orderNum = self.goodsInfo.orderNum
} else {
orderNum = self.genNumber4Order()
}
console.log(orderNum)

let submitOrderPara = {
'appId': baseParam.appId,
'shopCode': baseParam.shopCode,
'mallGoodsCode': self.curGoodsCode,
'orderNum': orderNum,
'leaveMsg': self.leaveMsg,
'payStyle': self.goodsInfo.payment,
'status': 'New',
'userName': self.guestInfo.name,
'userTel': self.guestInfo.phone
}
console.log('' + JSON.stringify(submitOrderPara))
self.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_ORDER), submitOrderPara).then((res) => {
if (res.body.code === 1) {
console.log('success:' + res.body.message)
let WxPayUnifiedOrderRequest = {
'appId': baseParam.appId,
'shopCode': baseParam.shopCode,
'attach': self.curGoodsCode,
'body': self.goodsInfo.title,
'outTradeNo': orderNum,
'totalFee': self.goodsInfo.shouldPay
}
self.$router.push({path: '/pay/weitallPay', query: {endTime, WxPayUnifiedOrderRequest: JSON.stringify(WxPayUnifiedOrderRequest)}})
} else {
self.$vux.alert.show({
title: res.body.message
})
}
})
}
})
} else {
self.$vux.alert.show({
content: ''
})
}
}
}
}



