console.log('start')
function Actor (play) {
  // console.log('ok0')
  //  
  this.play = play
  this.play.duration = 10000
  this.play.size = 16
  this.play.color = '#FFF'
  //  
  this.appearance = this.makeUp()
  //  
  this.appearance.hide().appendTo($('#barrage'))
}
//  
Actor.prototype.makeUp = function () {
  var appearance = $('<div style="min-width:400px;">' + '<img src="" class="userAvator">' + '<p style="background:rgba(0,0,0,0.5);border-radius:10px;display:inline-block;padding-left:10px;padding-right: 10px;font-size:' + this.play.size + ';color:' + this.play.color + ';">' + this.play.nickname + '' + this.play.redBagNum + '' + '</p></div>')
  appearance.children('.userAvator').attr('src', this.play.userAvatar).css({
    'width': '20px',
    'height': '20px',
    'position': 'relative',
    'top': '5px',
    'padding-right': '10px'
  })
  return appearance
}
//  
Actor.prototype.animate = function () {
  var that = this
  var appearance = that.appearance
  var mywidth = appearance.width()
  // console.log('animate')
  // left
  appearance.animate({
    left: -mywidth
  }, this.play.duration, 'swing', function () {
    appearance.hide()
  })
}
//  
Actor.prototype.perform = function () {
  // console.log('perform')
  var that = this
  var appearance = that.appearance
  // ,
  appearance.css({
    position: 'absolute',
    left: $('#barrage').width() + 'px',
    top: that.play.top||0,
    zIndex: 1,
    display: 'block'
  })
  //  
  //  
  //  
  var delayTime = that.play.time - (that.play.session - 1) * self.CheckTime
  setTimeout(function () {
    // console.log('animate-000')
    that.animate()
  }, delayTime)
}

module.exports = Actor



