
/* eslint-disable indent,no-extra-boolean-cast,no-underscore-dangle,padded-blocks,space-before-function-paren,fun-call-spacing,no-console,space-infix-ops */
import PaletteButton from '@/components/common/palette-button'
import FooterNav from '@/components/common/FooterNav'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
directives: {
TransferDom
},
data () {
return {
WxBannerv: [],
listCoupon: [],
showcoupons: false,
displayGroup1: {},
displayGroup2: [],
hotSale: '',
timeLimited: '',
recommended: '',
WxBannerh: [],
listChoiceness: []
}
},
computed: {
BannervHeight: function () {
return window.document.body.offsetWidth * 440 / 375 + 'px'
}
},
components: {
PaletteButton,
FooterNav,
Swiper,
SwiperItem,
TransferDom,
Popup,
XButton
},
created () {
var self = this
self.fetchData()
},
mounted () {
},
methods: {
fetchData () {
let self = this
this.showLoading()
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_MAIN_PAGEDATA), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
let mallPageData = res.body.data
/*  */
self.WxBannerv = mallPageData.areaA
if (!self.WxBannerv) {
return
}
if (!!self.WxBannerv && self.WxBannerv.length > 0) {
for (let i=0; i<self.WxBannerv.length; i++) {
self.WxBannerv[i] = self.getImageView(3, 440, 375, self.WxBannerv[i])
}
}
/* areaB- */
self.listCoupon = mallPageData.areaB
if (!self.listCoupon) {
return
}
if (!!self.listCoupon && self.listCoupon.length > 0) {
for (let j = self.listCoupon.length - 1; j >= 0; j--) {
self.listCoupon[j].startDate = self.formatDate(self.listCoupon[j].startDate, 'yyyy.MM.dd')
self.listCoupon[j].expireDate = self.formatDate(self.listCoupon[j].expireDate, 'yyyy.MM.dd')
}
}

/* areaC- */
self.displayGroup1 = mallPageData.areaC
self.displayGroup1.groupImg = self.getImageView(3, 275, 222, self.displayGroup1.groupImg)
console.log('display1:' + JSON.stringify(self.displayGroup1))
/* areaD-TOP */
self.displayGroup2 = mallPageData.areaD
if (!!self.displayGroup2 && self.displayGroup2.length > 0) {
for (var i = self.displayGroup2.length - 1; i >= 0; i--) {
self.displayGroup2[i].groupImg = self.getImageView(3, 375, 375, self.displayGroup2[i].groupImg)
console.log('display2:' + JSON.stringify(self.displayGroup2[i]))
}
self.hotSale = self.displayGroup2[0]
self.timeLimited = self.displayGroup2[1]
self.recommended = self.displayGroup2[2]
}
/* areaE -  */
self.WxBannerh = mallPageData.areaE
if (!self.WxBannerh) {
return
}
if (!!self.WxBannerh && self.WxBannerh.length > 0) {
for (let j=0; j<self.WxBannerh.length; j++) {
self.WxBannerh[j] = self.getImageView(3, 375, 187, self.WxBannerh[j])

}
}
/* areaF-  */
self.listChoiceness = mallPageData.areaF

if (!self.listChoiceness) {
return
}
if (!!self.listChoiceness && self.listChoiceness.length > 0) {
for (let k=0; k<self.listChoiceness.length; k++) {
self.listChoiceness[k].icon = self.getImageView(3, 185, 228, self.listChoiceness[k].icon)
console.log('goodsInfo = ' + JSON.stringify(self.listChoiceness[k]))
}
}
} else {
self.$vux.toast.text('', 'middle')
self.$router.go(-1)
}
})
},
toCouponPage () {
this.$router.push({name: 'myCouponList'})
},
receivedSta (sta) {
let result
if (sta) {
result = ''
} else {
result = ''
}
return result
},
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 1000)
},
viewMore () {
this.$router.push({path: '/mall/goodsList', query: {catagory: null}})
},
getCoupon (index) {
let self = this
self.listCoupon[index].received = true

/*  */

let getCouponPara = self.setBaseForm(cookie)
getCouponPara.shopCouponCode = self.listCoupon[index].code
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_COUPON_RECEIVE), getCouponPara).then((res) => {
console.log(res.body)
if (res.body.code === 1) {
console.log(res.body.message)
self.$vux.toast.text('', 'middle')
} else {
self.$vux.toast.text('', 'middle')
}
})
},
contactUs () {
window.location.href = 'tel://' + JSON.parse(window.sessionStorage.getItem('shopInfo')).telephone
}
}
}



