
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Loading from 'vux/src/components/loading/index.vue'

export default {
data () {
return {
activitiesList: []
}
},
components: {
XButton
},
computed: {
showIt: function () {
let self = this
let resultShow
if (!!self.activitiesList && self.activitiesList.length > 0) {
resultShow = false
} else {
resultShow = true
}
return resultShow
}
},
mounted () {
this.initPage()
},
methods: {
initPage () {
let self = this
console.log('activity')
this.$http.post(self.addAPIVersion(self.WtConst.API_MINE_ACTIVITIES), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
let result = res.body.data
if (!result) {
return
}
if (!!result.itemList && result.itemList.length > 0) {
self.activitiesList = result.itemList
}
console.log('res')
console.log(JSON.stringify(result))
}
})
},
getInvitationDetail (list) {
let self = this
self.$vux.loading.show({
text: 'Loading'
})


let baseParam = self.getBaseParam(cookie)

let getImgKeyParam = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
shopActivityId: list.activityId
}
console.log(getImgKeyParam)
this.$http.post(self.addAPIVersion(self.WtConst.API_MINE_ACTIVITY_INVITATIONCARD), getImgKeyParam).then((res) => {
console.log(res)

if (res.body.code === 1) {
self.$vux.loading.hide()
console.log(res.body.data)
let result = res.body.data
self.$router.push({ path: 'myActivities/invitationCard', query: { invitationCardImg: result.cardImg } })
} else {
self.$vux.toast.text(res.body.message, 'middle')
}
})
}
}
}



