
import Clocker from 'vux/src/components/clocker/index.vue'


export default {
props: ['compData'],
data () {
return {
showWarning: false
}
},
components: {
Clocker
},
computed: {
endTime: function () {
let self = this
let result = self.formatDate(self.compData, 'yyyy-MM-dd HH:mm:ss')
return result
}
}
}



