
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
directives: {
TransferDom
},
props: ['compData'],
computed: {
pageCode () {
return this.$route.params.pageCode
}
},
data () {
return {
show1: false
}
},
components: {
TransferDom,
Popup
},
mounted () {
console.log('on:' + this.pageCode)
this.initPage()
},
methods: {
initPage () {
let self = this
let curWidth = self.getViewportSize().width
let curHeight = self.getViewportSize().height
self.compData.shopIcon = !self.compData.shopIcon ? '' : self.getImageView(3, curWidth, curHeight, self.compData.shopIcon)
self.compData.shopQrcode = !self.compData.shopQrcode ? '' : self.getImageView(3, curWidth, curHeight, self.compData.shopQrcode)
console.log(self.compData.shopQrcode)
},
leaveTo (link) {
let self = this
let targetCode = ''
if (link.indexOf('home') > -1) {
targetCode = 'HOME'
} else if (link.indexOf('member') > -1) {
targetCode = 'MEMBER'
}
let baseParam = self.getBaseParam(cookie)
let DataFlowReportRequest = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
targetType: 'PAGE',
targetCode: targetCode,
fromType: 'PAGE',
fromCode: this.pageCode
}
self.$http.post(self.addAPIVersion(self.WtConst.API_DATA_COLLECTION_FLOW_REPORT), DataFlowReportRequest).then((res) => {
if (res.body.code === 1) {
console.log('report page data flow from qrcode success.')
}
})


window.location.href = link
},
focusUs () {
console.log(this.show1)
console.log(this.compData.shopQrcode)
this.show1 = true
}
}
}



