
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
props: ['compData', 'activityGift', 'activityId', 'headImg'],
directives: {
TransferDom
},
data () {
return {
isShow: false,
name: '',
contact: '',
babyBirth: ''
}
},
components: {
XDialog,
Group,
XButton,
XInput,
Datetime,
TransferDom
},
watch: {
compData: function () {
let self = this
self.isShow = self.compData
console.log('xxxx', self.compData)
}
},
computed: {
pageCode () {
return this.$route.params.pageCode
}
},
mounted () {
this.initPageData()
},
methods: {
initPageData () {
},
onFocus (val, $event) {
console.log('val', val, $event)
console.log('1111')
let self = this
/*  */
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM-DD',
value: '2017-05-20',
onConfirm (value) {
self.babyBirth = value
console.log('plugin confirm', value)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
getRedBagApi () {
/* API */
let self = this
let baseParam = this.getBaseParam(cookie)
console.log('')
console.log(self.isEmpty(self.name) || self.isEmpty(self.contact) || self.contact !== 11)
if (self.isEmpty(self.name)) {
self.$vux.toast.show({
position: 'middle',
text: '',
type: 'text'
});
} else if (self.isEmpty(self.contact) || self.contact.length !== 11) {
self.$vux.toast.show({
position: 'middle',
text: '',
type: 'text'
});
} else if (self.isEmpty(self.babyBirth)) {
self.$vux.toast.show({
position: 'middle',
text: '',
type: 'text'
});
} else {
let ActivityBabyShowGiftReceiveRequest = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
activityId: self.activityId,
name: self.name,
contact: self.contact,
content: self.babyBirth
}
/*  */
console.log(ActivityBabyShowGiftReceiveRequest)
/* API */
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_BABYSHOW_GIFT_RECEIVE), ActivityBabyShowGiftReceiveRequest).then((res) => {
console.log(res)
if (res.body.code === 1) {
self.$vux.toast.show({
position: 'middle',
type: 'success',
text: ''
});
self.isShow = false
self.$emit('update:compData', self.isShow)
} else {
self.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
}
})
}
},
hiddenForm () {
let self = this
console.log('okok')
self.isShow = false
self.$emit('update:compData', self.isShow)
},
change (value) {
console.log('change', value)
}
}
}



