import VueBarcode from '@xkeshi/vue-barcode'
export default {
  data () {
    return {
      shopInfo: this.getSessionStorageItem('shopInfo', true),
      list: this.$route.query.list
    }
  },
  computed: {
    barCode: function () {
      return this.list.code
    }
  },
  components: {
    VueBarcode
  }
}



