
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
props: ['compData'],
data () {
return {
WxPayUnifiedOrderRequest: {
'appId': this.getBaseParam(cookie).appId,
'shopCode': this.getBaseParam(cookie).shopCode,
'attach': '1513062510801',
'body': '',
'outTradeNo': 'T15196327' + new Date().getTime(),
'totalFee': 0.1
},
commodity: {}
}
},
components: {
XButton
},
computed: {
imgWidth: function () {
let self = this
let curWidth = self.getViewportSize().width
return Math.ceil(curWidth * 0.38)
}
},
mounted () {
this.initForm()
this.opDisable = this.commodity.opDisable
},
methods: {
confrimPay () {
let self = this
if (this.payType === 1) {
console.log('')
} else {
console.log('')
if (self.isWechatBrowser()) {
let baseParam = self.getBaseParam(cookie)
let WechatSignatureForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
href: window.location.href
}
self.$http.post(self.addAPIVersion(self.WtConst.API_SIGNATURE_JSAPI_GET), WechatSignatureForm).then((res1) => {

if (res1.body.code === 1) {
self.signature = res1.body.data
console.log('signature:{}', JSON.stringify(self.signature))
this.$wechat.config({
debug: true,
appId: self.signature.appId,
timestamp: self.signature.timest,
nonceStr: self.signature.nonceStr,
signature: self.signature.signature,
jsApiList: [
'chooseWXPay'
]
})
let WxPayUnifiedOrderRequest = self.WxPayUnifiedOrderRequest
WxPayUnifiedOrderRequest.tradeType = self.isWechatBrowser() ? 'JSAPI' : 'MWEB'

self.$http.post(self.WtConst.API_PLUGIN_WX_PAY_UNIFIED_ORDER, JSON.stringify(WxPayUnifiedOrderRequest)).then((result) => {
if (result.body.code === 1) {

let wxPayUnifiedOrderResult = result.body.data
console.log(wxPayUnifiedOrderResult)
if (self.isWechatBrowser()) {
console.log('1:' + wxPayUnifiedOrderResult.mwebUrl)
self.$wechat.ready(function () {
self.$wechat.chooseWXPay({
appId: wxPayUnifiedOrderResult.appId,
timestamp: wxPayUnifiedOrderResult.timeStamp,
nonceStr: wxPayUnifiedOrderResult.nonceStr,
package: wxPayUnifiedOrderResult.pkg,
signType: wxPayUnifiedOrderResult.signType,
paySign: wxPayUnifiedOrderResult.paySign,




success: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.success: ' + JSON.stringify(res))
self.$router.push({name: 'orderList'})
},
cancel: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.cancel: ' + JSON.stringify(res))
},
fail: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.fail: ' + JSON.stringify(res))
self.$vux.alert.show({
content: '' + JSON.stringify(res)
})

}
})
self.$wechat.error(function (res) {
console.log('editOrderInfo.vue, error: ' + JSON.stringify(res))
})
})
} else {
console.log('H5')
window.location.href = wxPayUnifiedOrderResult.mwebUrl
}
} else {
console.log('-{}', result.body.message)
self.$router.push({name: 'orderList'})
}
})
} else {

}
})
} else {
console.log('')
}
}
},
initForm () {
let self = this
self.commodity = self.compData
console.log(self.commodity)
self.commodity.img = !self.commodity.img ? '' : self.getImageView(3, self.imgWidth, self.imgWidth, self.commodity.img)
console.log(self.commodity.img)
self.opDisable = self.commodity.opDisable
console.log(self.opDisable)
},
getReward () {
console.log('getReward')
let self = this
let baseParam = self.getBaseParam(cookie)
let commodityReceiveParam = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
targetType: self.commodity.targetType,
targetCode: self.commodity.targetCode,
scaCode: self.commodity.code
}
console.log('commodityReceiveParam:' + commodityReceiveParam)
self.$http.post(self.addAPIVersion(self.WtConst.API_MALL_TRADE_COMMODITY_ACTIVITY_RECEIVE), commodityReceiveParam).then((res) => {
console.log(res)
if (res.body.code === 1) {

self.commodity.opDisable = true
self.commodity.numLeft--

self.$vux.toast.text('', 'middle')
} else {
self.$vux.toast.text(res.body.message, 'middle')
}
})
console.log('getReward')
}
}
}



