
import NavHeaderAA from '@/marketing/common/NavHeaderAA'
import ActivityCommodityBB from '@/marketing/common/ActivityCommodityBB'
import Popup from 'vux/src/components/popup/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Badge from 'vux/src/components/badge/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import SlideBarrage from './slideBarrage.js'

export default {
directives: {
TransferDom
},
data() {
return {
showWarning: false,
navHeaderAAComp: {
'code': 'NavHeaderAA',
'show': true,
'data': {
'shopHome': '',
'shopQrcode': '',
'shopIcon': '',
'shopName': '',
'hotLine': '',
'shopMember': '',
'shopAddress': ''
}
},
currentUser: {},
activityId: '',
activityState: 0,
marketingNotice: '',
showNotice: true,
startDate: '',
endDate: '',
ruleStart: '',
ruleEnd: '',
startScreenBgImg: '',
musicIcon: '',
musicSrc: '',
hotLineIcon: '',
barrageIcon: '',
momBarrageIcon: '',
blissCardPlaceholderImg: '',
blissCardImgs: [],
gameJoinInfo: {
userCode: '',
nickname: '',
avatar: '',
content: ''
},

rewardDisplayImg: '',
barrageList: [],
activityPromotionImgsA: [],
activityPromotionImgsB: [],
activityPromotionImgsC: [],
storePromotionImgs: [],
activityJoinFormItems: '',
rewardPlaceholderImg: '',
joinCondition: '',
ruleDesc: '',
playCount: '',
footerNav: [
{icon: this.getImgFromQiniu('c4effe262e554c78ae5e5ac5016f86e2'), title: 'Ta'},
{icon: this.getImgFromQiniu('ef464c06ed6f4a00a2d9381282cdcad1'), title: ''},
{icon: this.getImgFromQiniu('b69570190181434a816b0f319793c6e2'), title: ''}
],
activityJoinFormItems: {
momName: '',
momMobile: ''
},
resultZero: {},
extracFortuneImg: '',
blessingText: '',
belssingPlaceholder: '20',
isGameJoinInfo: false,
showOrderForm: false,
showBarrage: false,
showBlessingPage: false
};
},
computed: {
menuWidth: function () {
let self = this
let res
if (self.footerNav.length > 5) {
res = 1 / 3 * 100 + '%'
} else {
res = 1 / self.footerNav.length * 100 + '%'
}
return res
},
pageCode() {
return this.$route.params.pageCode
},
r() {
return this.$route.query.r
},
p() {
return this.$route.query.p
},











Rewardquantity() {
let self = this
let resultQuantity
let rewardRes = self.blissCardImgs.filter((item) => {
if (item.quantity) {
return item.quantity > 0
}
})
if (!!rewardRes && rewardRes.length > 0) {
resultQuantity = rewardRes.length
} else {
resultQuantity = 0
}
return resultQuantity
}
},
components: {
Popup,
NavHeaderAA,
Clocker,
Badge,
Marquee,
MarqueeItem,
XImg,
XDialog,
Group,
XInput,
XButton,
XTextarea,
cookie
},
watch: {
barrageList: function () {
let self = this
self.playCount = self.barrageList.length
let sessionTmp = 0
for (let j = 0; j < self.barrageList.length; j++) {
let is2Top = (sessionTmp + 1) * 30
if (is2Top > 150) {
sessionTmp = 0
} else {
sessionTmp++
}
self.barrageList[j].top = (sessionTmp + 1) * 40
}
self.initBarrage()
}
},
mounted() {
let self = this;

self.getPageData();

/* DOMContentLoadedWeixinJSBridgeReady */
document.addEventListener('DOMContentLoaded', function () {
self.audioAutoPlay()
})
},
methods: {
doWechatOperation() {
let baseParam = this.getBaseParam(cookie);
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode,
href: window.location.href
};
let reportRequest = {
shopCode: baseParam.shopCode,
sourceType: 'MT_ACTIVITY_MOM_SHOW',
sourceCode: this.activityId,
pageCode: this.pageCode,
parentUser: this.p ? this.p : '',
};
let bizParams = {
r: this.r,
p: this.currentUser.code // parent
}
this.setWxOperate(wxOperateForm, reportRequest, bizParams)
},

getPageData() {
let self = this
let baseParam = self.getBaseParam(cookie)
let ActivityPageDataRequest = {
shopCode: baseParam.shopCode,
pageCode: self.pageCode,
r: self.r,
p: self.p
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_MOOSHOW_PAGEDATA), ActivityPageDataRequest).then((res) => {
if (res.body.code === 1) {
let result = res.body.data
self.currentUser = result.currentUser;
this.doWechatOperation();

if (!!result.navHeaderAAComp) {
if (!!result.navHeaderAAComp.data.shopIcon) {
result.navHeaderAAComp.data.shopIcon = self.getImageView(3, 375, 375, result.navHeaderAAComp.data.shopIcon)
}
if (!!result.navHeaderAAComp.data.shopQrcode) {
result.navHeaderAAComp.data.shopQrcode = self.getImageView(3, 375, 375, result.navHeaderAAComp.data.shopQrcode)
}
self.navHeaderAAComp = result.navHeaderAAComp
}
console.log(result)
let normalComp = result.normalComp
document.title = normalComp.title

self.activityId = normalComp.id
self.activityState = normalComp.state
self.startDate = normalComp.startDate
self.endDate = self.formatDate(normalComp.endDate, 'yyyy-MM-dd HH:mm:ss')
self.ruleStart = self.getDate(normalComp.startDate)
self.ruleEnd = self.getDate(normalComp.endDate)
self.joinCondition = normalComp.joinCondition
self.ruleDesc = normalComp.ruleDesc

if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.showNotice = true
self.marketingNotice = self.getMarketingNotice(self.activityState)
} else {
self.showNotice = false
}

if (normalComp.bgType === 'COLOR') {
$('#maMomShow').css({
'background': normalComp.bgVal
})
} else {
$('#maMomShow').css({
'background-image': 'url(' + normalComp.bgVal + ')'
})
}


self.musicIcon = self.getImgFromQiniu(normalComp.musicImg)

self.musicSrc = self.getAudioFromQiniu(normalComp.bgMusicKey)


self.hotLineIcon = self.getImgFromQiniu(normalComp.hotLineImg)
normalComp.barrageIcon = normalComp.barrageIcon.split(',')
if (!!normalComp.barrageIcon && normalComp.barrageIcon.length > 0) {
for (let tmpBarr = 0; tmpBarr < normalComp.barrageIcon.length; tmpBarr++) {
normalComp.barrageIcon[tmpBarr] = self.getImgFromQiniu(normalComp.barrageIcon[tmpBarr])
}
self.barrageIcon = normalComp.barrageIcon
}
self.momBarrageIcon = self.barrageIcon[0]

self.startScreenBgImg = self.getImageView(3, 375, 375, normalComp.bgImg)

if (!!normalComp.blissCardPlaceholderImg) {
normalComp.blissCardPlaceholderImg = self.getImgFromQiniu(normalComp.blissCardPlaceholderImg)
self.blissCardPlaceholderImg = normalComp.blissCardPlaceholderImg
}
console.log('')
console.log(self.blissCardPlaceholderImgs)

if (!!normalComp.blissCardImgs && normalComp.blissCardImgs.length > 0) {
for (let j = normalComp.blissCardImgs.length - 1; j >= 0; j--) {

if (!!normalComp.blissCardImgs[j].firstKey) {
normalComp.blissCardImgs[j].firstKey = self.getImgFromQiniu(normalComp.blissCardImgs[j].firstKey)
}
if (!!normalComp.blissCardImgs[j].secondKey) {
normalComp.blissCardImgs[j].secondKey = self.getImgFromQiniu(normalComp.blissCardImgs[j].secondKey)
}
if (!normalComp.blissCardImgs[j].quantity) {
normalComp.blissCardImgs[j].quantity = 0
}
}
self.blissCardImgs = normalComp.blissCardImgs
}

if (!normalComp.gameJoinInfo) {
self.isGameJoinInfo = false
}
if (!!normalComp.gameJoinInfo && !!normalComp.gameJoinInfo.avatar) {
self.isGameJoinInfo = true
if (normalComp.gameJoinInfo.avatar.indexOf('http') === -1) {
normalComp.gameJoinInfo.avatar = self.getImgFromQiniu(normalComp.gameJoinInfo.avatar)
}
self.gameJoinInfo = normalComp.gameJoinInfo
}

self.activityPromotionImgsA = self.getFormatImg(normalComp.activityPromotionImgsA)
self.activityPromotionImgsB = self.getFormatImg(normalComp.activityPromotionImgsB)
self.activityPromotionImgsC = self.getFormatImg(normalComp.activityPromotionImgsC)
self.storePromotionImgs = self.getFormatImg(normalComp.storePromotionImgs)

if (!!normalComp.rewardPlaceholderImg) {
self.rewardPlaceholderImg = self.getImgFromQiniu(normalComp.rewardPlaceholderImg)
}

self.rewardDisplayImg = self.getImageView(3, 375, 375, normalComp.rewardDisplayImg);

if (!!normalComp.barrageList && normalComp.barrageList.length > 0) {
for (let ii = 0; ii < normalComp.barrageList.length; ii++) {
if (!!normalComp.barrageList[ii].userAvatar) {
if (normalComp.barrageList[ii].userAvatar.indexOf('http') === -1) {
normalComp.barrageList[ii].userAvatar = self.getImgFromQiniu(normalComp.barrageList[ii].userAvatar)
}
normalComp.barrageList[ii].createTime = self.formatDate(normalComp.barrageList[ii].createTime, 'yyyy-MM-dd')
}
self.barrageList = normalComp.barrageList
}
}
console.log('')
self.$emit('changeLoadState', true)
} else {
self.$vux.toast.text('')
}
})

$('.ma-hotline').addClass('animated infinite tada')
},
getFormatImg(resultImgs) {
let self = this
if (!!resultImgs) {
resultImgs = resultImgs.split(',')
if (!!resultImgs && resultImgs.length > 0) {
for (let tmpA = 0; tmpA < resultImgs.length; tmpA++) {
if (!self.isEmpty(resultImgs[tmpA])) {
resultImgs[tmpA] = self.getImageView(3, 375, 375, resultImgs[tmpA])
} else {
resultImgs.length -= 1
}
}
return resultImgs
}
}
},
initBarrage() {

let self = this
self.showBarrage = true
self.momBarrageIcon = self.barrageIcon[0]
let director = $({})
$.each(self.barrageList, function (k, item) {

let session = k
item.session = session
let slideBarrage = new SlideBarrage(item)
director.on(session + 'slideStart', function () {
slideBarrage.perform()
})
})
let currentSession = 0
let slideInt = setInterval(function () {
director.trigger(currentSession + 'slideStart')
if (currentSession === self.playCount) {
currentSession = 0
} else {
currentSession++
}
}, 4000)
},
changeBarrageState() {
let self = this
self.showBarrage = !self.showBarrage
if (self.showBarrage) {
self.momBarrageIcon = self.barrageIcon[0]
} else {
self.momBarrageIcon = self.barrageIcon[1]
}
},
audioAutoPlay() {
let audio = $('.ma-musicSrc').get(0)
document.addEventListener("WeixinJSBridgeReady", function () {
audio.play()
$('.ma-musicControl').addClass('animated infinite rotateAll')
}, false)
},
controlPlay() {

let audio = $('.ma-musicSrc').get(0)
if (audio.paused) {
audio.play()
$('.ma-musicControl').addClass('animated infinite rotateAll')
} else {
audio.pause()
$('.ma-musicControl').removeClass('animated infinite rotateAll')
}
},
viewTheFortune(num) {
let viewResult
if (num > 0) {
viewResult = true
} else {
viewResult = false
}
return viewResult
},
getDate(time) {
let self = this
self.formatDate(time, 'yyyymmdd')
return self.formatDate(time, 'yyyymmdd')
},
success(src, ele) {
const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error(src, ele, msg) {
const span = ele.parentNode.querySelector('span')
span.innerText = 'load error'
},
contactUs() {
window.location.href = 'tel://' + this.navHeaderAAComp.data.hotLine
},
getMarketingNotice(id) {
let tmpNotice
if (id === -1) {
tmpNotice = ''
} else if (id === 1) {
tmpNotice = ''
} else if (id === 2) {
tmpNotice = ''
} else {
tmpNotice = ''
}
return tmpNotice
},
doMenuMethods(index) {
let self = this
if (self.footerNav[index].title === 'Ta') {
if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.$vux.toast.text(self.getMarketingNotice(self.activityState))
} else {
let momShowBlissCardCollectRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
r: self.r,
p: self.p,
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_MOOSHOW_BLISSCARD_COLLECT), momShowBlissCardCollectRequest).then((collectRes) => {
if (collectRes.body.code === 1) {
self.resultZero = collectRes.body.data
self.blissCardImgs.map(function (item) {
if (item.code === self.resultZero.blissCardCode) {
item.quantity += 1
self.extracFortuneImg = item.secondKey
let tmpBarrage = {
id: self.resultZero.id,
quantity: 1,
userCode: self.r,
nickname: self.resultZero.currentUserName,
userAvatar: self.resultZero.currentUserAvatar,
materialName: item.name,
createTime: self.formatDate(self.resultZero.createTime, 'yyyy-MM-dd')
}
self.barrageList.push(tmpBarrage)
}
})
self.showBlessingPage = true
} else {
self.$vux.toast.text(collectRes.body.message)
}
})
}
} else if (self.footerNav[index].title === '') {
if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.$vux.toast.text(self.getMarketingNotice(self.activityState))
} else {
self.showOrderForm = true
}
} else if (self.footerNav[index].title === '') {
self.$router.push({path: self.navHeaderAAComp.data.shopHome})
} else {
}
},
pushOrderForm() {
let self = this
if (self.isEmpty(self.activityJoinFormItems.momName)) {
self.$vux.toast.text('')
} else if (!self.isMobile(self.activityJoinFormItems.momMobile)) {
self.$vux.toast.text('')
} else {
let momSHowReservationRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
momName: self.activityJoinFormItems.momName,
momMobile: self.activityJoinFormItems.momMobile,
r: self.r,
p: self.p,
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_MOOSHOW_RESERVATION), momSHowReservationRequest).then((reservationRes) => {
if (reservationRes.body.code === 1) {
self.$vux.toast.text('')
self.activityJoinFormItems.momName = ''
self.activityJoinFormItems.momMobile = ''
self.showOrderForm = false
} else {
let msg = reservationRes.body.message ? reservationRes.body.message : '';
self.$vux.toast.text(msg)
}
})
}
},
pushBlessingForm() {
let self = this
if (self.isEmpty(self.blessingText)) {
self.$vux.toast.text('')
} else {
let momShowBlissCardUpdateRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
incomeId: self.resultZero.id,
remark: self.blessingText
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_MOOSHOW_BLISSCARD_UPDATE), momShowBlissCardUpdateRequest).then((updateRes) => {
if (updateRes.body.code === 1) {
self.barrageList.map((item) => {
if (item.id === self.resultZero.id) {
item.remark = self.blessingText
}
})
self.showBlessingPage = false
self.$vux.toast.text('')
} else {
self.$vux.toast.warn('')
}
})
}
}
}
};



