
import XProgress from 'vux/src/components/x-progress/index.vue'


export default {
name: 'activityCommodityAA',
components: {
XProgress
},
props: {
compData: Array,
totalLoves: {
type: Number,
default: 0
}
},
computed: {
tmpTotalLoves() {
return this.totalLoves;
}
},
data() {
return {}
},
methods: {
currentPercent(condition) {
let percent = (this.tmpTotalLoves / condition) * 100;
if (percent >= 100) {
return 100;
} else {
return percent;
}
},
currentLockStatus(condition) {
let percent = (this.tmpTotalLoves / condition) * 100;
if (percent >= 100) {
return ''
} else {
return '';
}
}
}
};



