
import FooterNav from '@/components/common/FooterNav'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
mainImg: '/static/img/scoreShop/memberTask.png',
taskLists: []
}
},
components: {
FooterNav,
Group,
Cell
},
computed: {
},
mounted () {
console.log(this.$store.state.userScore)
},
created () {
this.getTaskList()
},
methods: {
getTaskList () {
let self = this
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_TASK_TODO_LIST), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
console.log(res.body.data)
self.taskLists = res.body.data
}
})
},
taskStatus (list) {
let res
if (list.status === 'FINISH') {
res = ''
} else if (list.completion === list.quantity) {
res = ''
} else {
res = ''
}
return res
},
doNext (list) {
let self = this
if (list.status === 'FINISH') {
self.$vux.toast.text('')
} else if (list.status === 'SUCCESS') {
list.status = 'FINISH'
let taskFinishHead = self.setBaseForm(cookie)
taskFinishHead.taskCode = list.taskCode
console.log('19010')
self.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_TASK_FINISH), taskFinishHead).then((res) => {
console.log(res)
if (res.body.code === 1) {
console.log(res)
let result = res.body.data
self.$vux.toast.text(' + ' + result.reward, 'middle')
list.status = 'FINISH'
} else {
self.$vux.toast.text(res.body.message, 'middle')
}
})
} else {
console.log(self.getQueryString(list.link, 'rt'))
let rt = self.getQueryString(list.link, 'rt')
if (rt && rt === 'tt') {
let taskDetectHead = self.setBaseForm(cookie)
taskDetectHead.rt = 'tt'
taskDetectHead.taskCode = list.taskCode


self.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_TASK_DETECT), taskDetectHead).then((res) => {
console.log(res)
if (res.body.code === 1) {
console.log(res)
self.$router.push({path: list.link})
}
})
} else {
console.log('19033')
self.$router.push({path: list.link})
}
}
},
getQueryString (link, name) {
var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
var r = link.substr(1).match(reg)
if (r !== null) {
return unescape(r[2])
} else {
return null
}
}
}
}



