import Vue from 'vue'
import Router from 'vue-router'
import VueRouterTitle from 'vue-router-title'
import '@/assets/js/qiniu.js'
import '@/assets/css/common.css'
import '@/assets/css/animate.css'
import '@/assets/iconfont/iconfont.js'
import '@/assets/iconfont/iconfont.css'
import Home from '@/components/Home'
//
import scoreShop from '@/components/scoreShop/scoreShop'
import scoreDetail from '@/components/scoreShop/scoreDetail'
import memberTask from '@/components/scoreShop/memberTask'
import memberAchieve from '@/components/scoreShop/memberAchieve'
import exchangeRecord from '@/components/scoreShop/exchangeRecord'
import exchangeDetail from '@/components/scoreShop/exchangeDetail'
import myCouponList from '@/components/scoreShop/myCouponList'
import myCouponDetail from '@/components/scoreShop/myCouponDetail'
//
import mall from '@/components/mall/mall'
import GoodsList from '@/components/mall/goodsList'
import goodsDetail from '@/components/mall/goodsDetail'
import editOrderInfo from '@/components/mall/editOrderInfo'
import orderConfirm from '@/components/mall/orderConfirm'
import orderList from '@/components/mall/orderList'
import orderDetail from '@/components/mall/orderDetail'
import weitallPay from '@/components/pay/weitallPay'
import weitallPayDemo from '@/components/pay/demo'
import weitallPayDemo2 from '@/components/pay/demo2'
// 
import shopShow from '@/components/shopShow'
//
import Photos from '@/components/product/photos'
//
import AboutUs from '@/components/product/aboutUs'
//
import ShopInfo from '@/components/ShopInfo'
//
import NewestActivity from '@/components/interactive/NewestActivity'
// 
import Member from '@/components/customer/member'
// 
import ManageAccount from '@/components/customer/manageAccount/manageAccount'
import setUserInfo from '@/components/customer/manageAccount/setUserInfo'
import SetPassword from '@/components/customer/manageAccount/setPassword'
// [,,,,]
import OrderStates from '@/components/customer/orderStates/orderStates'
import ShopCart from '@/components/customer/orderStates/shopCart'
import MyGifts from '@/components/customer/myGifts'
import GiftReceived from '@/components/customer/giftReceived'
import DiscountCoupon from '@/components/customer/discountCoupon'
import DiscountCode from '@/components/customer/discountCode'
import GiftCard from '@/components/customer/giftCard/giftCard'
import PurchaseRecord from '@/components/customer/giftCard/purchaseRecord'
import GivingRecord from '@/components/customer/giftCard/givingRecord'
import MyIntegral from '@/components/customer/myIntegral'
import MyVipCard from '@/components/customer/vipCard/myVipCard'
import InfoSet from '@/components/customer/vipCard/infoSet'
import MyActivities from '@/components/customer/MyActivities/MyActivities'
import InvitationCard from '@/components/customer/MyActivities/InvitationCard'
// 
import Qrcode from '@/marketing/activity/qrcode/Qrcode'
import BabyShow from '@/marketing/activity/babyShow/BabyShow'
// import maThemeShow from '@/marketing/activity/themeShow/ThemeShow'
import marketing from '@/marketing/activity/marketing'


import errorPage401 from '@/components/common/401.vue'
// import '@/assets/js/swiper.min.js'
// import '@/assets/css/swiper.min.css'

Vue.use(Router)
const scrollBehavior = (to, from, savedPosition) => {
  const position = {}
  if (savedPosition) {
    return savedPosition
  } else {
    position.x = 0
    position.y = 0
  }
  // console.log(position);
  return position
}
const router = new Router({
  mode: 'history',
  base: __dirname,
  scrollBehavior,
  routes: [
    {path: '/', redirect: '/homeMain'},
    { path: '/home', name: 'Home', component: Home, meta: { title: '' } },
    // ----------------   - 
    { path: '/index', name: 'Home', component: Home, meta: {title: ''} },
    { path: '/main', name: 'Home', component: Home, meta: {title: ''} },
    { path: '/homeMain', name: 'Home', component: Home, meta: {title: ''} },
    // ----------------   - 

    {path: '/shopShow', name: 'shopShow', component: shopShow, meta: {title: ''}},
    {path: '/home/photos/:category', component: Photos},
    {
      path: '/aboutUs', component: AboutUs,
      meta: {title: ''}
    },
    {
      path: '/shopInfo', component: ShopInfo,
      meta: {title: ''}
    },
    {
      path: '/activity/newest', component: NewestActivity,
      meta: {title: ''}
    },
    {
      path: '/member', name: 'member', component: Member,
      meta: {title: ''}
    },
    {
      path: '/scoreShop', name: 'scoreShop', component: scoreShop,
      // beforeEnter: (to, from, next) => {
      //   // TODO: 
      //   console.log('router: beforeEnter')
      //   // next()
      // },
      meta: {title: ''}
    },
    {
      path: '/scoreShop/scoreDetail', name: 'scoreDetail', component: scoreDetail,
      meta: {title: ''}
    },
    {
      path: '/scoreShop/memberTask', name: 'memberTask', component: memberTask,
      meta: {title: ''}
    },
    {
      path: '/scoreShop/memberAchieve', name: 'memberAchieve', component: memberAchieve,
      meta: {title: ''}
    },
    {
      path: '/scoreShop/exchangeRecord', name: 'exchangeRecord', component: exchangeRecord,
      meta: {title: ''}
    },
    {
      path: '/scoreShop/myCouponList', name: 'myCouponList', component: myCouponList,
      // beforeEnter: (to, from, next) => {
      //   // TODO: 
      //   console.log('router: beforeEnter')
      //   // next()
      // },
      meta: {title: ''}
    },
    {
      path: '/scoreShop/myCouponList/myCouponDetail', name: 'myCouponDetail', component: myCouponDetail,
      meta: {title: ''}
    },
    {
      path: '/scoreShop/:id/exchangeDetail', name: 'exchangeDetail', component: exchangeDetail,
      meta: {title: ''}
    },
    {
      path: '/mall', name: 'mall', component: mall,
      meta: {title: ''}
    },
    {
      path: '/mall/goodsList', component: GoodsList,
      meta: {title: ''}
    },
    {
      path: '/mall/goodsList/goodsDetail', name: 'goodsDetail', component: goodsDetail,
      meta: {title: ''}
    },
    {
      path: '/mall/editOrderInfo', name: 'editOrderInfo', component: editOrderInfo,
      meta: {title: ''},
      children: [
        {path: 'orderConfirm', name: 'orderConfirm', component: orderConfirm}
      ]
    },
    {
      path: '/mall/orderList', name: 'orderList', component: orderList,
      // beforeEnter: (to, from, next) => {
      //   // TODO: 
      //   console.log('router: beforeEnter')
      //   // next()
      // },
      meta: {title: ''}
    },
    {
      path: '/mall/orderList/orderDetail', name: 'orderDetail', component: orderDetail,
      meta: {title: ''}
    },
    {
      path: '/pay/weitallPay', component: weitallPay, name: 'weitallPay',
      meta: {title: ''}
    },
    {
      path: '/pay/demo', component: weitallPayDemo, name: 'weitallPayDemo',
      meta: {title: ''}
    },
    {
      path: '/pay/demo2', component: weitallPayDemo2, name: 'weitallPayDemo2',
      meta: {title: '2'}
    },
    {
      path: '/member/manageAccount', name: 'manageAccount', component: ManageAccount,
      meta: {title: ''},
      children: [
        {
          path: 'setUserInfo', name: 'setUserInfo', component: setUserInfo,
          meta: {title: ''}
        },
        {
          path: 'setPassword', name: 'setPassword', component: SetPassword,
          meta: {title: ''}
        }
      ]
    },
    {path: '/member/orderStates', name: 'orderStates', component: OrderStates},
    {
      path: '/member/shopCart', name: 'shopCart', component: ShopCart,
      meta: {title: ''}
    },
    {
      path: '/member/myGifts', name: 'myGifts', component: MyGifts,
      meta: {title: ''}
    },
    {
      path: '/member/myVipCard', name: 'myVipCard', component: MyVipCard,
      meta: {title: ''},
      children: [
        {
          path: 'infoSet', name: 'infoSet', component: InfoSet,
          meta: {title: ''}
        }
      ]
    },
    {path: '/member/myIntegral', name: 'myIntegral', component: MyIntegral},
    {
      path: '/member/giftCard', name: 'giftCard', component: GiftCard,
      meta: {title: ''},
      children: [
        {
          path: 'purchaseRecord', name: 'purchaseRecord', component: PurchaseRecord,
          meta: {title: ''}
        },
        {
          path: 'givingRecord', name: 'givingRecord', component: GivingRecord,
          meta: {title: ''}
        }
      ]
    },
    {
      path: '/member/giftReceived', name: 'giftReceived', component: GiftReceived,
      meta: {title: ''}
    },
    {
      path: '/member/discountCoupon', name: 'discountCoupon', component: DiscountCoupon,
      meta: {title: ''}
    },
    {
      path: '/member/discountCode', name: 'discountCode', component: DiscountCode,
      meta: {title: ''}
    },
    {
      path: '/member/myActivities', name: 'myActivities', component: MyActivities,
      meta: {title: ''},
      children: [
        {
          path: 'invitationCard', name: 'invitationCard', component: InvitationCard,
          meta: {title: ''}
        }
      ]
    },
    {path: '/marketing/activity/qrcode/:pageCode', name: 'qrcode', component: Qrcode, meta: {title: ''}},
    {path: '/marketing/activity/babyShow/:pageCode', name: 'babyShow', component: BabyShow, meta: {title: ''}},
    // { path: '/marketing/:pageCode', name: 'ThemeShow', component: ThemeShow, meta: { title: '' } },
    {path: '/marketing/:pageCode', name: 'marketing', component: marketing, meta: {title: ''}},
    {
      path: '/errorPage/401', name: 'error401', component: errorPage401,
      meta: {title: '401'}
    }
    // { path: '*', component: NotFoundComponent }
  ]
})
export default router
VueRouterTitle({router})



