export default {
  props: ['compData'],
  data () {
    return {
      advertisementContent: ''
    }
  },
  computed: {
  },
  mounted () {
    this.initPageData()
  },
  methods: {
    initPageData () {
      let self = this
      if (!self.compData) {
        self.advertisementContent = ''
      } else {
        self.advertisementContent = self.compData
      }
    }
  }
}



