
import cookie from 'vux/src/tools/cookie/index.js'


export default {
props: ['compData'],
components: {
cookie
},
data() {
return {
footerMenus: ''
}
},
computed: {
pageCode () {
return this.$route.params.pageCode
},

menuWidth: function () {
let self = this
let res
if (self.footerMenus.length > 5) {
res = 1 / 3 * 100 + '%'
} else {
res = 1 / self.footerMenus.length * 100 + '%'
}
console.log(res)
return res
},
menuHeight: function () {
let self = this
let res
if (self.footerMenus.length > 5) {
res = '140px'
} else {
res = '60px'
}
console.log(res)
return res
}
},
mounted() {
this.initPageData()
},
methods: {
initPageData() {
let self = this
if (!self.compData.footerMenus) {
return
} else {
self.footerMenus = self.compData.footerMenus
}
},
leaveTo(list) {
let self = this
if (list.title === 'TA') {

self.$emit('leaveTo')
self.showForm = true
} else {
console.log(list.link)
window.location.href = list.link
}
},
reportData() {
let self = this;
let baseParam = self.getBaseParam(cookie);
let DataFlowReportRequest = {
shopCode: baseParam.shopCode,
targetType: 'ACTION_CONTACT', // PAGE|GOODS|PRODCUT|ACTION_SUBSCRIBE|ACTION_CONTACT
targetCode: self.pageCode, // pageCode|goodsCode|productCode|shopCode|pageCode
fromType: 'PAGE', // 'SHARE|PAGE'
fromCode: self.pageCode
};
self.$http.post(self.addAPIVersion(self.WtConst.API_DATA_COLLECTION_FLOW_REPORT), DataFlowReportRequest).then((res) => {
if (res.body.code === 1) {

} else {

}
});
}
}
}



