
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
serviceEva: { productQuality: 0, shopPro: 0, shopService: 0, productSpeed: 0 },
orderNum: this.$route.query.orderNum,
order: '',
evaluation: '',
canEvaluate: ''
}
},
computed: {
isFullPay: function () {
return this.order.payStyle === ''
},
isComplete: function () {
return this.order.evaluated === 0
}
},
components: {
XButton,
Cell,
Group,
XTextarea,
Rater,
Divider,
Card
},
beforeMount () {
},
mounted () {
},
created () {
this.getOrderDetail()
},
methods: {
getOrderDetail () {
let self = this
let requestHead = self.setBaseForm()
requestHead.orderNum = self.orderNum
console.log('xx' + JSON.stringify(requestHead))
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_ORDER_DETAIL), requestHead).then((res) => {
if (res.body.code === 1) {
self.order = res.body.data
console.log(self.order)
self.order.icon = self.getImageView(3, 170, 93, self.order.icon)
if (self.order.status === 'PAY_FAIL') {
self.order.status = ''
} else if (self.order.status === 'SUCCESS_FULL' || self.order.status === 'SUCCESS_DEPOSIT') {
self.order.status = ''
} else if (self.order.status === 'FINISH') {

self.evaluation = self.order.content
self.evalutaor = self.order.evalutaor
if (self.order.evaluated === 0) {
self.order.status = ''
self.canEvaluate = false
} else {
self.order.status = ''
self.canEvaluate = true
}
} else {
self.order.status = ''
}
if (self.order.payStyle === 'FULL') {
self.order.payStyle = ''
self.order.shouldPay = self.order.preferentialPrice
} else {
self.order.payStyle = ''
self.order.shouldPay = self.order.deposit
}
self.order.createTime = self.formatDate(self.order.createTime, 'yyyy-MM-dd HH:mm:ss')
let endTime = parseInt(self.order.createTime + self.order.expireIn * 60000, 10)
self.order.endTime = self.formatDate(endTime, 'yyyy-MM-dd HH:mm:ss')
self.order.oweMoney = self.order.preferentialPrice - self.order.deposit
console.log(self.order)
} else {
console.log(res.body.message)
}
})
},
goShop () {
console.log('')
},
contactUs () {
window.location.href = 'tel://' + this.order.shopTel

},
pushEvaluation () {
let self = this
let requestHead = {
'appId': self.getBaseParam(cookie).appId,
'shopCode': self.getBaseParam(cookie).shopCode,
'mallGoodsCode': self.order.mallGoodsCode,
'orderNum': this.orderNum,
'content': self.evaluation
}
console.log(requestHead)
if (self.order.status === '') {
self.canEvaluate = true
} else {
self.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_EVALUATE), requestHead).then((res) => {
if (res.body.code === 1) {
console.log(res.body.message)
self.$vux.toast.text('', 'middle')
self.order.status = ''
self.canEvaluate = true
} else {
console.log('11111')
self.$vux.toast.text(res.body.message, 'middle')
}
})
}
},
goToPay () {
let WxPayUnifiedOrderRequest = {
'appId': this.getBaseParam(cookie).appId,
'shopCode': this.getBaseParam(cookie).shopCode,
'attach': this.order.mallGoodsCode,
'body': this.order.title,
'outTradeNo': this.orderNum,
'totalFee': this.order.shouldPay
}
this.$router.push({path: '/pay/weitallPay', query: {endTime: this.order.endTime, WxPayUnifiedOrderRequest: JSON.stringify(WxPayUnifiedOrderRequest)}})
},
cancelAction (sta) {
let cancelLink
if (sta === '') {
cancelLink = ''
} else if (sta === '') {
cancelLink = ''
} else {
cancelLink = ''
}
this.$vux.alert.show({
title: cancelLink,
onHide () {
console.log('hide')
},
onShow () {
console.log('show')
}
})
},
deleteOrder () {
let self = this
self.updateOrderStatus(this.order, 'DELETED')
},
reBuyOne () {
this.$router.push({name: 'goodsDetail', query: {mallGoodsCode: this.order.mallGoodsCode}})
},
onEvent (event) {
console.log('on', event)
}
}
}



