export default {
  props: ['compData'],
  data () {
    return {
      supportList: [],
      viewSupportList: false
    }
  },
  computed: {
  },
  mounted () {
    this.initPageData()
  },
  methods: {
    initPageData () {
      let self = this
      if (!self.compData) {
        self.viewSupportList = false
      }
      if (self.compData && self.compData.length > 0) {
        self.viewSupportList = true
        self.supportList = self.compData
        console.log(self.supportList)
      }
    }
  }
}



