import * as types from '../mutations-types'

const actions = {
  updateAppId({commit}, appId) {
    commit(types.UPDATE_APPID, appId)
  },
  updateUserToken({commit}, userToken) {
    commit(types.UPDATE_USER_TOKEN, userToken)
  },

  updateShopCode({commit}, shopCode) {
    commit(types.UPDATE_SHOPCODE, shopCode)
  },
  updateShopTheme({commit}, shopTheme) {
    commit(types.UPDATE_SHOPTHEME, shopTheme)
  },
  setUserScore({commit}, userScore) {
    commit(types.SET_USERSCORE, userScore)
  }
}
export default actions



