
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
directives: {
TransferDom
},
props: ['gift', 'mallGoodsCode', 'userName', 'uesrTel'],
data () {
return {
showOrder22: false
}
},
computed: {
guestInfo: function () {
let res
if (!JSON.parse(window.sessionStorage.getItem('guestInfo'))) {
res = {
name: self.userName,
phone: self.userTel
}
} else {
res = JSON.parse(window.sessionStorage.getItem('guestInfo'))
}
return res
}
},
mounted () {
},
components: {
TransferDom,
Popup,
Group,
XInput,
XButton
},
methods: {
getOrderPage () {
this.showOrder22 = true
console.log('ok')
},
log (str) {
console.log(str)
},
gotoOrder () {
let self = this
let re = /^1[3|4|5|8][0-9]\d{4,8}$/
if (self.guestInfo.name === '') {
self.$vux.alert.show({
content: '',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
self.$router.push({path: '/mall/goodsList/goodsDetail'})
}
})
} else if (!re.test(self.guestInfo.phone)) {
self.$vux.alert.show({
content: '',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
self.$router.push({path: '/mall/goodsList/goodsDetail'})
}
})
} else {
console.log(JSON.stringify(this.guestInfo))
window.sessionStorage.setItem('guestInfo', JSON.stringify(self.guestInfo))
let reservePara = {
'appId': 'wxe739b7cc0eb350fe',
'shopCode': '33010271508231965584',
'mallGoodsCode': self.mallGoodsCode,
'userName': self.guestInfo.name,
'userTel': self.guestInfo.phone
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_RESERVAE), reservePara).then((res) => {
if (res.body.code === 1) {
console.log(res.body)
self.$vux.alert.show({
title: '',
content: '',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
self.showOrder22 = false
}
})
}
})
}
}
}
}



