
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
props: ['compData', 'activityBaseInfo', 'activityId'],
directives: {
TransferDom
},
data () {
return {
isShow: false,
showNum: false,
randomNum: '',
numLists: [ '1', '5', '10' ],
curBagNum: '1'
}
},
components: {
Checker,
CheckerItem,
XDialog,
XButton,
XInput,
Icon,
TransferDom
},
watch: {
compData: function () {
let self = this
self.isShow = self.compData
console.log('xxxx', self.compData)
}
},
methods: {
sendRedBag () {
let self = this
let baseParam = self.getBaseParam(cookie)
/* API */
let ActivityBabyShowRedBagPayRequest = {
shopCode: baseParam.shopCode,
activityId: self.activityId,
quantity: self.curBagNum
}
console.log(ActivityBabyShowRedBagPayRequest)
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_BABYSHOW_REDBAG_PAY), ActivityBabyShowRedBagPayRequest).then((res) => {
console.log(res)
if (res.body.code === 1) {
self.hiddenForm()
let result = res.body.data
let WxPayUnifiedOrderRequest = {
'appId': result.appId,
'attach': result.attach,
'body': result.body,
'outTradeNo': result.outTradeNo,
'totalFee': result.totalFee
}
console.log(WxPayUnifiedOrderRequest)
if (self.isWechatBrowser()) {
let WechatSignatureForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
href: window.location.href
}
self.$http.post(self.addAPIVersion(self.WtConst.API_SIGNATURE_JSAPI_GET), WechatSignatureForm).then((res1) => {

if (res1.body.code === 1) {
self.signature = res1.body.data
console.log('signature:{}', JSON.stringify(self.signature))
this.$wechat.config({
debug: false,
appId: self.signature.appId,
timestamp: self.signature.timest,
nonceStr: self.signature.nonceStr,
signature: self.signature.signature,
jsApiList: [
'chooseWXPay'
]
})
WxPayUnifiedOrderRequest.tradeType = self.isWechatBrowser() ? 'JSAPI' : 'MWEB'

self.$http.post(self.WtConst.API_PLUGIN_WX_PAY_UNIFIED_ORDER, JSON.stringify(WxPayUnifiedOrderRequest)).then((result1) => {
if (result1.body.code === 1) {
let wxPayUnifiedOrderResult = result1.body.data
console.log(wxPayUnifiedOrderResult)
if (self.isWechatBrowser()) {
self.$wechat.ready(function () {
self.$wechat.chooseWXPay({
appId: wxPayUnifiedOrderResult.appId,
timestamp: wxPayUnifiedOrderResult.timeStamp,
nonceStr: wxPayUnifiedOrderResult.nonceStr,
package: wxPayUnifiedOrderResult.pkg,
signType: wxPayUnifiedOrderResult.signType,
paySign: wxPayUnifiedOrderResult.paySign,
success: function (res2) {
self.$emit('sendBagSuccess', { curBagNum: self.curBagNum })
},
cancel: function (res2) {
},
fail: function (res2) {
console.log('sendBagForm.vue, chooseWXPay.fail: ' + JSON.stringify(res2))
self.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
}
})
self.$wechat.error(function (res2) {
console.log('sendBagForm.vue, error: ' + JSON.stringify(res2))
})
})
} else {
console.log('H5')
window.location.href = wxPayUnifiedOrderResult.mwebUrl
}
} else {
console.log('-{}', result.body.message)

}
})
} else {

}
})
} else {
console.log('')
}
}
})
self.showSendForm = false
},
getRedBag (isRandom) {
/*  */
if (isRandom) {
this.curBagNum = Math.ceil(Math.random() * 50)

$('.randomBag').removeClass('redbag4-item').addClass('redbag4-item-selected')
} else {
$('.randomBag').addClass('redbag4-item').removeClass('redbag4-item-selected')
}
},
getConfirmPage () {
let self = this
self.$emit('update:compData', false)
self.showNum = true
},
hiddenForm () {
let self = this
self.$emit('update:compData', false)
},
doConfirm () {
let self = this
self.showNum = false
console.log('on confirm')
console.log(this.curBagNum)
let curTimes = new Date().getTime()
console.log(self.activityBaseInfo.activityEndDate)
if (self.activityBaseInfo.activityEndDate <= curTimes) {
this.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
} else if (self.activityBaseInfo.activityStartDate > curTimes) {
this.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
} else {
self.sendRedBag()
}
}
}
}



