
import WshopFooterNav from '@/components/common/MallFooterNav'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
directives: {
TransferDom
},
data () {
return {
realName: '',
mobile: '',
showbutton: false,
goodsInfo: {},
mainImgs: [],
infoModule: [],
giftOrder: '',
giftFullBuy: '',
giftToStore: '',
couponList: [],
evluations: [],
evaNum: 3,
toggleView: true,
isphoto: true,
showTop: false,
showcoupons: false,
introDesc: '',
introImgs: '',
setContents: []
}
},
components: {
Swiper,
SwiperItem,
TransferDom,
Popup,
WshopFooterNav,
XButton,
Divider,
Card,
Tab,
TabItem,
XImg,
Group,
Cell,
Sticky,
CellFormPreview
},
computed: {
couponListMain: function () {
return this.couponList.slice(0, 3)
},
curGoodsCode: function () {
let mallCode
if (this.$route.query.mallGoodsCode) {

mallCode = this.$route.query.mallGoodsCode
window.sessionStorage.setItem('mallGoodsCode', this.$route.query.mallGoodsCode)
} else {

mallCode = window.sessionStorage.getItem('mallGoodsCode')
}
return mallCode
},
evluationNum: function () {
let self = this
if (self.evluations.length === 0) {
self.showbutton = false
} else {
self.showbutton = true
}
return self.evluations.length
}
},
watch: {
},
created () {
this.getGoodsDetail()
},
mounted () {
window.addEventListener('scroll', this.isgoTop)
},
updated () {



},
methods: {
getGoodsDetail () {
let self = this
let baseParam = self.getBaseParam(cookie)
let requestHead = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
mallGoodsCode: self.curGoodsCode
}
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_GOODS_DETAIL), requestHead).then((res) => {
if (res.body.code === 1) {
let result = res.body.data
console.log(result)
/* areaA */
self.mainImgs = result.mainImgs
if (!self.mainImgs) {
return
}
if (!!self.mainImgs && self.mainImgs.length > 0) {
for (var i = 0; i < self.mainImgs.length; i++) {
self.mainImgs[i] = self.getImageView(3, 375, 282, self.mainImgs[i])
}

}
/* areaB */
self.goodsInfo.title = result.title
self.goodsInfo.typeDesc = result.typeDesc
self.goodsInfo.preferentialPrice = result.preferentialPrice
self.goodsInfo.price = result.price
self.goodsInfo.deposit = result.deposit
self.goodsInfo.soldNum = result.soldNum
self.goodsInfo.shopName = result.shopName
self.goodsInfo.mainImg = result.mainImgs[0]
/* areaC */
self.infoModule = result.pkgDesc


/*  */
self.deposit = result.deposit
/*  */
self.giftOrder = result.giftOrder
/*  */
self.giftFullBuy = result.giftFullBuy
/*  */
self.giftToStore = result.giftToStore
/* -couponList */
self.couponList = result.couponList
if (!self.couponList) {
return
}
if (!!self.couponList && self.couponList.length > 0) {
for (var j = self.couponList.length - 1; j >= 0; j--) {
self.couponList[j].startDate = self.formatDate(self.couponList[j].startDate, 'yyyy.MM.dd')
self.couponList[j].expireDate = self.formatDate(self.couponList[j].expireDate, 'yyyy.MM.dd')
}

}
self.evluations = result.mallGoodsEvaluationList
if (!self.evluations) {
return
}
if (!!self.evluations && self.evluations.length > 0) {
for (var k = self.evluations.length - 1; k >= 0; k--) {
self.evluations[k].createTime = self.formatDate(self.evluations[k].createTime, 'yyyy-MM-dd')
}

}
/* - */
self.introDesc = result.introDesc
/* - */
self.introImgs = result.introImgs
if (!self.introImgs) {
return
}
if (!!self.introImgs && self.introImgs.length > 0) {
for (var ii = self.introImgs.length - 1; ii >= 0; ii--) {
self.introImgs[ii] = self.getImageView(3, 375, null, self.introImgs[ii])
}

}
/*  */
self.setContents = result.pkgContent
self.realName = result.realName
self.mobile = result.mobile
console.log(self.setContents)
}
})
},
togglephoto (state) {
this.isphoto = state


document.documentElement.scrollTop = document.getElementById('photoDetails').offsetTop
document.body.scrollTop = document.getElementById('photoDetails').offsetTop

},
success (src, ele) {

const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error (src, ele, msg) {
console.log('error load', msg, src)
const span = ele.parentNode.querySelector('span')
span.innerText = 'load error'
},
receivedSta (sta) {
let result
if (sta) {
result = ''
} else {
result = ''
}
return result
},
evaluate: function (evaNum) {
let self = this

return self.evluations.slice(0, evaNum)
},
viewAllEvluations () {
let self = this
if (!!this.evluations && this.evluations.length > 3) {
this.evaNum = this.evluations.length
this.toggleView = false
} else {
self.$vux.toast.text('', 'middle')
}



},
closeAllEvluations () {

this.evaNum = 3
this.toggleView = true



},
isgoTop () {
this.scroll = document.documentElement.scrollTop
this.scoll = document.body.scrollTop

if (this.scroll > 400 || this.scoll > 400) {
this.showTop = true
console.log('showTop' + this.showTop)
} else {
this.showTop = false
}
console.log('showTop' + this.showTop)
},
backToTop () {
document.documentElement.scrollTop = 0
document.body.scrollTop = 0



},
getCoupon (index) {
let self = this
self.couponList[index].received = true

/*  */
let baseParam = self.getBaseParam(cookie)
let getCouponPara = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
shopCouponCode: self.couponList[index].code
}
this.$http.post(self.addAPIVersion(self.WtConst.API_MALL_COUPON_RECEIVE), getCouponPara).then((res) => {
console.log(res.body)
if (res.body.code === 1) {
console.log(res.body.message)
self.$vux.toast.text('', 'middle')
} else {
self.$vux.toast.text('', 'middle')
}
})
},
submitNewOrder () {
let self = this
self.$router.push({path: '/mall/editOrderInfo', query: {mallGoodsCode: self.curGoodsCode, realName: self.realName, mobile: self.mobile, goodsInfo: JSON.stringify(self.goodsInfo)}})
}
}
}



