
/* eslint-disable spaced-comment,fun-call-spacing,space-before-function-paren,semi,no-console */
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'


export default {
data () {
return {
mobile: '',
userKey: this.$route.query.userKey,
tmpUserKey: '',
inputType: ''
}
},
components: {
Group,
XInput,
XButton,
Selector,
Datetime
},
computed: {
placeholderTmp: function () {
let result
let self = this
if (self.userKey === 'mobile') {
result = ''
self.inputType = 'china-mobile'
} else if (self.userKey === 'realName') {
result = ''
self.inputType = 'china-name'
}
return result
}
},
mounted () {
let self = this
if (!!JSON.parse(window.sessionStorage.getItem('userInfo'))) {
let userInfoTmp = JSON.parse(window.sessionStorage.getItem('userInfo'))
if (self.userKey === 'sex') {
if (userInfoTmp.sex === 1) {
userInfoTmp.sex = ''
} else if (userInfoTmp === 2) {
userInfoTmp = ''
} else {
userInfoTmp = ''
}
console.log(userInfoTmp.sex)
self.tmpUserKey = userInfoTmp.sex
} else if (self.userKey === 'birthday') {
self.tmpUserKey = userInfoTmp.birthday
}
}
},
methods: {
change (value) {
console.log('change', value)
},
success () {
let self = this
let tmp = self.userKey
if (tmp === 'mobile') {
if (self.tmpUserKey && self.tmpUserKey.length === 11) {
let baseParam = self.getBaseParam(cookie)
let WechatUserConfigForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
mobile: self.tmpUserKey
}

self.$http.post(self.addAPIVersion(self.WtConst.API_MINE_INFO_SET), WechatUserConfigForm).then((res) => {
if (res.body.code === 1) {
self.$vux.toast.text('')
self.$router.push({name: 'member'})
} else {
self.$vux.toast.text(res.body.message)
}
})
} else {
self.$vux.toast.text('')
}
} else if (tmp === 'realName') {
let baseParam = self.getBaseParam(cookie)
let WechatUserConfigForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
realName: self.tmpUserKey
}
console.log(WechatUserConfigForm)
self.$http.post(self.addAPIVersion(self.WtConst.API_MINE_INFO_SET), WechatUserConfigForm).then((res) => {
if (res.body.code === 1) {
self.$vux.toast.text('')
self.$router.push({name: 'member'})
} else {
self.$vux.toast.text(res.body.message)
}
})
} else if (tmp === 'sex') {
let tmpUserSex
if (self.tmpUserKey === '') {
tmpUserSex = 1
} else if (self.tmpUserKey === '') {
tmpUserSex = 2
} else {
tmpUserSex = 0
}
console.log('tmpUserSex', tmpUserSex)
let baseParam = self.getBaseParam(cookie)
let WechatUserConfigForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
sex: tmpUserSex
}
console.log(WechatUserConfigForm)
self.$http.post(self.addAPIVersion(self.WtConst.API_MINE_INFO_SET), WechatUserConfigForm).then((res) => {
if (res.body.code === 1) {
if (tmpUserSex === 1) {
self.tmpUserKey = ''
} else if (tmpUserSex === 2) {
self.tmpUserKey = ''
} else {
self.tmpUserKey = ''
}
console.log(self.tmpUserKey)
self.$vux.toast.text('')
self.$router.push({name: 'member'})
} else {
self.$vux.toast.text(res.body.message)
}
})
} else if (tmp === 'birthday') {
let baseParam = self.getBaseParam(cookie)
let WechatUserConfigForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
birthday: self.tmpUserKey
}
console.log(WechatUserConfigForm)
self.$http.post(self.addAPIVersion(self.WtConst.API_MINE_INFO_SET), WechatUserConfigForm).then((res) => {
if (res.body.code === 1) {
self.$vux.toast.text('')
self.$router.push({name: 'member'})
} else {
self.$vux.toast.text(res.body.message)
}
})
}
}
}
}



