// console.log('SlideBarrageStart')
let self = this
function SlideBarrage (item) {
  // console.log('ok0')
  //  
  this.item = item
  /*  */
  this.item.duration = 10000
  /*  */
  this.item.size = '16px'
  /*  */
  this.item.color = '#FFF'
  /*  */
  this.appearance = this.makeUp()
  /*  */
  this.appearance.hide().appendTo($('#tals-slide-barrage'))
}
//  
SlideBarrage.prototype.makeUp = function () {
  // console.log('')
  // console.log(this.item)
  let appearance = $("<div style='min-width:400px;height:30px;line-height:30px;text-align:left;'><p class='singleBarrage'>" + this.item.nickname + '' + this.item.quantity + this.item.spName +  '<img src="" class="giftImg">' + "</p></div>")
  appearance.children('.singleBarrage').css({
    'position': 'relative',
    'max-width': '90%',
    'display': 'inline-block',
    'border-radius': this.item.size,
    'background': 'rgba(0, 0, 0, 0.5)',
    'font-size': this.item.size,
    'color': this.item.color,
    'padding-left': '10px',
    'padding-right': '32px',
    'overflow': 'hidden',
    'text-overflow':'ellipsis',
    'white-space': 'nowrap'
  })
  appearance.find('.giftImg').attr('src', this.item.spSecondKey).css({
    'width': '20px',
    'height': '20px',
    'position': 'absolute',
    'right': '10px'
  })
  // console.log(appearance)
  return appearance
}
//  
SlideBarrage.prototype.animate = function () {
  let that = this
  let appearance = that.appearance
  let screenWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
  // left
  appearance.animate({
    left: screenWidth
  }, this.item.duration, 'swing', function () {
    appearance.hide()
  })
}
//  
SlideBarrage.prototype.perform = function () {
  // console.log('perform')
  let that = this
  let appearance = that.appearance
  let mywidth = appearance.width()
  // console.log(that.item.top)
  // ,
  appearance.css({
    position: 'absolute',
    left: -mywidth,
    top: that.item.top,
    zIndex: 1,
    display: 'block'
  })
  let delayTime = that.item.time - (that.item.session - 1) * self.CheckTime
  setTimeout(function () {
    that.animate()
  }, delayTime)
}

module.exports = SlideBarrage



