
import Cell from 'vux/src/components/cell/index.vue'





import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
data () {
return {
time: '',
value1: '',
vcode1: '',
vcode2: '',
warn1: false,
warn2: false,
show: false,
disabled: false
}
},
components: {
Cell,
Group,
XInput,
XButton,
Alert
},
computed: {
textVcode: function () {
return this.time > 0 ? this.time + 's ' : '';
}
},
methods: {
run () {
/**/
this.vcode2 = 1357
console.log('')
this.disabled = true
this.time = 60
this.timer()
},
timer () {
if (this.time > 0) {
this.time--;
setTimeout(this.timer, 1000);
}else{
this.disabled = false;
}
},
success () {
console.log(this.vcode2)
if(this.value1){
if(this.vcode1 && this.vcode1 == this.vcode2){
this.show = true
}
else{
this.warn1 = true
}
}else{
this.warn2 = true
}
},
onHide (x) {
if(x)
this.$router.push({path: '/member'})
else
this.$router.push({name: 'setPassword'})
},
onShow () {
console.log('on show')
}
}
}



