
import FooterNav from '@/components/common/FooterNav'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
userScore: this.$store.state.userScore,
isobtained: false,
unFinishTaskList: [],
finishTaskList: [],
show1: true
}
},
components: {
FooterNav,
Tab,
TabItem,
Group,
Cell,
XProgress,
Loading
},
created () {
this.getAchievementList()
},
mounted () {

},
methods: {
getAchievementList () {
let self = this
setTimeout(() => {
this.show1 = false
}, 500)
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_TASK_ACHIEVEMENT_LIST), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {

let data = res.body.data
if (!data || data.length <= 0) {
return
}
for (let i = 0; i < data.length; i++) {
data[i].icon = 'icon-jine'
data[i].iconBg = '#FCB03C'
data[i].progress = self.progress(data[i])
if (data[i].status === 1) {
self.finishTaskList.push(data[i])
} else {
self.unFinishTaskList.push(data[i])
}
}
}
})
},
progress (item) {
let percent = parseInt(Math.round(item.completion / item.quantity * 100), 10)
if (percent >= 100) {
percent = 100
}
return percent
},
notComplete () {
let self = this
self.$vux.alert.show({
content: ''
})

}
}
}



