
/* eslint-disable spaced-comment,fun-call-spacing,semi,space-before-function-paren,no-console,no-trailing-spaces,no-underscore-dangle,no-empty */

import wx from 'weixin-js-sdk'
import FooterNav from '@/components/common/FooterNav'
import Cell from 'vux/src/components/cell/index.vue';
import Group from 'vux/src/components/group/index.vue';
import cookie from 'vux/src/tools/cookie/index.js'


export default {
name: 'member',
data () {
return {
initInfo: {},
shopInfo: {},
userInfo: {},
mainImg: 'static/img/member/member_mainImg.png',
allOrders: {
id: 'member_1',
title: '',
icon: 'icon-allorder',
isShow: true,
orders: []
},
order_states: [
{ id: 'orderstate_1', title: '', icon: 'icon-fukuan', isShow: true },
{ id: 'orderstate_2', title: '', icon: 'icon-daijiedan', isShow: true },
{ id: 'orderstate_3', title: '', icon: 'icon-daifahuo', isShow: true },
{ id: 'orderstate_4', title: '', icon: 'icon-yifahuo', isShow: true }
],
shopcart: {
id: 'member_2',
title: '',
icon: 'icon-shopcart',
link: 'shopCart',
isShow: false
},
memberMenus: [
{id: 'member_3', title: '', icon: 'icon-vip_card', link: '/member/myVipCard', isShow: false },
{id: 'member_4', title: '', icon: 'icon-gift_card', link: '/member/giftCard', isShow: false },
{id: 'member_5', title: '', icon: 'icon-jifen', link: '/scoreShop', isShow: true },
{id: 'member_6', title: '', icon: 'icon-gift', link: '/member/giftReceived', isShow: false },
{id: 'member_7', title: '', icon: 'icon-discount_coupon', link: '/scoreShop/myCouponList', isShow: true },
{id: 'member_8', title: '',icon: 'icon-discount_code', link: '/member/discountCode',isShow: true },
{id: 'member_9', title: '', icon: 'icon-zengpin', link: '/member/myGifts', isShow: false },
{id: 'member_10', title: '', icon: 'icon-wodeyaoqingqia', link: '/member/myActivities', isShow: true },
],
manageAccount: {
id: 'member_10',
title: '',
icon: 'icon-manage_account',
link: '/manageAccount',
isShow: true
}
}
},
components: {
FooterNav,
Group,
Cell
},
beforeMount () {
let len = this.order_states.length;
let singleOrder = this.order_states;
let allO = this.allOrders.orders;
for (var i = 0; i < len; i++) {
allO = allO.concat(singleOrder[i].orders);

}
this.allOrders.orders = allO
console.log(JSON.stringify(this.allOrders));
},
mounted () {
this.initBaseShopInfo()
this.initPageData()

let baseParam = this.getBaseParam(cookie)
console.log('mounted, baseParam:' + JSON.stringify(baseParam))
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: 'MEMBER',
href: window.location.href
}
this.setWxOperate(wxOperateForm)
},
methods: {
initBaseShopInfo () {
let self = this
let baseParam = self.getBaseParam(cookie)
console.log('initBaseShopInfo, baseParam:' + JSON.stringify(baseParam))

let baseShopInfo = self.getBaseShopInfoFromSessionStorage()
let isNeedToRefreshBaseShopInfo = (self.isEmpty(baseShopInfo) ||
self.isEmpty(baseShopInfo.shopInfo) ||// self.isEmpty(baseShopInfo.navModuleList) ||// self.isEmpty(baseShopInfo.mainModuleList) ||
(baseParam.appId !== baseShopInfo.shopInfo.appId && baseParam.shopCode !== baseShopInfo.shopInfo.shopCode))
console.log('isNeedToRefreshBaseShopInfo:' + isNeedToRefreshBaseShopInfo)
if (isNeedToRefreshBaseShopInfo) {
console.log('refresh base shop info.')
self.$http.post(self.addAPIVersion(self.WtConst.API_SHOP_MAIN_PAGEDATA), {
appId: baseParam.appId,
shopCode: baseParam.shopCode
}).then((res) => {
if (res.body.code === 1) {

let tmpResult = self.formatBaseShopInfo(res.body.data)
self.shopInfo = tmpResult.shopInfo


} else {

console.error('base shop info query fail.')
return
}
})
} else {
self.shopInfo = baseShopInfo.shopInfo


console.log('shopInfo.shopName:' + JSON.stringify(self.shopInfo.name))
}
},
initPageData: function () {
let self = this;
this.$http.post(self.addAPIVersion(self.WtConst.API_MINE_PAGEDATA), self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
self.userInfo = res.body.data

window.sessionStorage.setItem('userInfo', JSON.stringify(self.userInfo))
}
})
}
}
}



