
import PaletteButton from '@/components/common/palette-button'
import footerNav from '@/components/common/FooterNav'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
shopInfo: {},
mainImg: '',
advantageImg: '',
selectGroup: {},
radioGroup: {},
inputGroups: [],
likePhotoType: '',
likeTime: '',
inputUserTel: '',
inputMsgTool: '',
userTel: '',
msgTool: ''
}
},
components: {
PaletteButton,
footerNav,
Group,
XInput,
XButton,
Grid,
GridItem,
Checker,
CheckerItem,
CheckIcon,
cookie
},
created () {
this.initBaseShopInfo()
},
watch: {
'$route': 'initBaseShopInfo'
},

mounted () {
this.initPageData()

let baseParam = this.getBaseParam(cookie)
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: 'ACTIVITY_FORM',
href: window.location.href
}
this.setWxOperate(wxOperateForm)
},
methods: {
initBaseShopInfo () {
let self = this
let baseParam = self.getBaseParam(cookie)
console.log('baseParam:' + JSON.stringify(baseParam))

let baseShopInfo = self.getBaseShopInfoFromSessionStorage()
let isNeedToRefreshBaseShopInfo = (self.isEmpty(baseShopInfo) ||
self.isEmpty(baseShopInfo.shopInfo) ||// self.isEmpty(baseShopInfo.navModuleList) ||// self.isEmpty(baseShopInfo.mainModuleList) ||
(baseParam.appId !== baseShopInfo.shopInfo.appId && baseParam.shopCode !== baseShopInfo.shopInfo.shopCode))
console.log('isNeedToRefreshBaseShopInfo:' + isNeedToRefreshBaseShopInfo)
if (isNeedToRefreshBaseShopInfo) {
console.log('refresh base shop info.')
self.$http.post(self.addAPIVersion(self.WtConst.API_SHOP_MAIN_PAGEDATA), {
appId: baseParam.appId,
shopCode: baseParam.shopCode
}).then((res) => {
if (res.body.code === 1) {

let tmpResult = self.formatBaseShopInfo(res.body.data)
self.shopInfo = tmpResult.shopInfo


} else {

console.error('base shop info query fail.')
return
}
})
} else {
self.shopInfo = baseShopInfo.shopInfo


console.log('shopInfo.shopName:' + JSON.stringify(self.shopInfo.name))
}
},
initPageData () {
let self = this
this.$http.post(self.WtConst.API_INTERACTIVE_ACTIVITY_NEWEST_PAGEDATA, self.setBaseForm(cookie)).then((res) => {
if (res.body.code === 1) {
let pageData = res.body.data
if (!pageData) {
return
}
self.mainImg = pageData.mainImg ? self.getImageView(3, 640, 493, pageData.mainImg) : ''
self.advantageImg = pageData.advantageImg ? self.getImageView(3, 1000, 498, pageData.advantageImg) : ''
if (pageData.groups && pageData.groups.length > 0) {
for (let i = 0; i < pageData.groups.length; i++) {
let group = pageData.groups[i]
let type = group.type
let content = group.content
if (type === 'select') {
self.selectGroup = group
self.selectGroup.options = self.buildCheckListOptions(JSON.parse(content))

} else if (type === 'radio') {
self.radioGroup = group
self.radioGroup.options = self.buildCheckListOptions(JSON.parse(content))
self.radioGroup.max = 1

} else if (type === 'input') {
self.inputGroups.push(group)

}
}
}
if (!self.inputGroups) {
return
}
if (!!self.inputGroups && self.inputGroups.length > 0) {
for (let j = 0; j < self.inputGroups.length; j++) {
console.log(self.inputGroups[j])
if (self.inputGroups[j].code === 'userTel') {
self.inputUserTel = self.inputGroups[j]
}
if (self.inputGroups[j].code === 'msgTool') {
self.inputMsgTool = self.inputGroups[j]
}
}
}
console.log(self.inputUserTel)
console.log(self.inputMsgTool)
}
})
},
buildCheckListOptions (content) {
if (!content) {
return []
}
let ops = []
for (let key in content) {
ops.push({
key: content[key],
value: content[key]
})
}
return ops
},
success () {
let self = this
let ActivityNewestJoinForm = self.setBaseForm(cookie)
ActivityNewestJoinForm.source = window.sessionStorage.getItem('source')
ActivityNewestJoinForm.userTel = self.userTel
ActivityNewestJoinForm.msgTool = self.msgTool
ActivityNewestJoinForm.groups = [
{
name: self.selectGroup.name,
content: JSON.stringify(self.likePhotoType)
}, {
name: self.radioGroup.name,
content: JSON.stringify(self.likeTime)
}
]
if (self.isEmpty(ActivityNewestJoinForm.userTel) && self.isEmpty(ActivityNewestJoinForm.msgTool)) {
self.$vux.toast.text('', 'top')
return
}

console.log('ActivityNewestJoinForm:' + JSON.stringify(ActivityNewestJoinForm))
self.$http.post(self.WtConst.API_INTERACTIVE_ACTIVITY_NEWEST_JOIN, ActivityNewestJoinForm).then((res) => {
let title = ''
let content = ''
let path = '/home'
if (res.body.code !== 1) {
title = ''
content = ''
path = '/activity/newest'
}
this.$vux.alert.show({
title: title,
content: content,
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding')
self.$router.push({path: path})
}
})
})
},
contactUs () {
window.location.href = 'tel://' + JSON.parse(window.sessionStorage.getItem('shopInfo')).telephone
}
}
}



