
import NavFooterAA from '@/marketing/common/NavFooterAA'
import NavHeaderAA from '@/marketing/common/NavHeaderAA'
import TextAA from '@/marketing/common/TextAA'
import CountDownAA from '@/marketing/common/CountDownAA'
import PopupAA from '@/marketing/common/PopupAA'
import AnimationAA from '@/marketing/common/AnimationAA'
import AnimationBB from '@/marketing/common/AnimationBB'
import ActivityCommodityAA from '@/marketing/common/ActivityCommodityAA'
import ActivityCommodityBB from '@/marketing/common/ActivityCommodityBB'
import Actor from '@/assets/js/babyShow.js'
import SendBagForm from './SendBagForm'
import GetBagForm from './GetBagForm'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import cookie from 'vux/src/tools/cookie/index.js'


export default {
directives: {
TransferDom
},
components: {
ViewBox,
NavHeaderAA,
NavFooterAA,
TextAA,
CountDownAA,
PopupAA,
AnimationAA,
AnimationBB,
SendBagForm,
GetBagForm,
ActivityCommodityAA,
ActivityCommodityBB,
Group,
Icon,
Checker,
CheckerItem,
XInput,
Swiper,
SwiperItem,
XDialog,
XButton,
Popup,
Clocker,
TransferDom,
cookie
},
data() {
return {
showNotice: false,
marketingNotice: '',
userInfo: {},
isDownload: false,
curIndex: 0,
activityId: '',
activityState: '',
compList: [],
normalComp: {},
activityBaseInfo: {},
activityRule: {
show: false,
title: '',
content: ''
},
activityGift: {},
activityRewardList: [],
playCount: '',
baseImgs: [],
babyShowImgs: [],
giftGuideImgs: [],
giftReceiveImgs: [],
storePromotionImgs: [],
isSendLove: false,
footer_01: {
code: 'NavFooterAA',
show: true,
data: {
footerMenus: [
{code: '', title: '', link: '', icon: '/static/img/babyShow/zhuye.svg'},
{code: 'callHotLine', title: '', link: '', icon: '/static/img/babyShow/kefu.svg'}
]
}
},
barrageInfoList: [],
backgroundMusic: '',
curShowPage: '',
CheckTime: 4000,
showBarrage: true,
showSendForm: false,
showGetForm: false
}
},
watch: {




curIndex: function () {
/*  */
let self = this
console.log('self.curIndex:', self.curIndex)
if (self.curIndex < self.babyShowImgs.length) {

$('#middleImg, #littleImg, #introduceText, #introduceImg, #bagTitle, #red-bag-hand, #red-bag-body').css({
'display': 'none'
})
} else if (self.curIndex === self.babyShowImgs.length) {
$('#bagTitle, #red-bag-hand, #red-bag-body').css({
'display': 'none'
})
/* 1 */
self.animationAAStart()
} else if (self.curIndex === self.babyShowImgs.length + 1) {
$('#middleImg, #littleImg, #introduceText, #introduceImg').css({
'display': 'none'
})
/* 2 */
self.animationBBStart()
} else {

$('#middleImg, #littleImg, #introduceText, #introduceImg, #bagTitle, #red-bag-hand, #red-bag-body').css({
'display': 'none'
})
}
},
barrageInfoList: function () {
let self = this
self.playCount = self.barrageInfoList.length
let sessionTmp = 0
for (let j = 0; j < self.barrageInfoList.length; j++) {
if (!self.barrageInfoList[j].userAvatar) {
self.barrageInfoList[j].userAvatar = '/static/img/babyShow/1.png'
}
self.barrageInfoList[j].time = 2000 * j
let is2Top = (sessionTmp + 1) * 30
if (is2Top > 150) {
sessionTmp = 0
} else {
sessionTmp++
}
self.barrageInfoList[j].top = (sessionTmp + 1) * 30
}
console.log('')
console.log(self.barrageInfoList)
self.initBarrage()
}
},
computed: {
pageCode() {
return this.$route.params.pageCode
},
r() {
return this.$route.query.r
},
/*  */
swiperHeight: function () {
let self = this
return self.getViewportSize().height + 'px'
}
},
beforeMount() {
},
mounted() {
let self = this
let baseParam = this.getBaseParam(cookie)
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode,
href: window.location.href
}
self.setWxOperate(wxOperateForm)
/* DOMContentLoadedWeixinJSBridgeReady */
document.addEventListener('DOMContentLoaded', function () {
self.audioAutoPlay()
})
/*  */
self.getPageData()
},
methods: {
getPageData() {
console.log(new Date().getTime())
let self = this
const wd = self.getViewportSize().width
let baseParam = self.getBaseParam(cookie)

/*  */
self.$http.post(self.addAPIVersion(self.WtConst.API_MINE_PAGEDATA), baseParam).then((res) => {
if (res.body.code === 1) {
self.userInfo = res.body.data
}
})
if (!self.userInfo.avatar) {
/*  */
self.userInfo.avatar = '/static/img/babyShow/2.png'
}
let pageDataRequest = {
appId: baseParam.appId, // 
shopCode: baseParam.shopCode, // 
pageCode: this.pageCode, // 
r: self.r
}


self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_BABYSHOW_PAGEDATA), pageDataRequest).then((res) => {
console.log(res)
if (res.body.code === 1) {
let result = res.body.data
console.log(result)
self.activityId = result.activityId
self.activityState = result.activityState

if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.showNotice = true
if (self.activityState === -1) {
self.marketingNotice = ''
} else if (self.activityState === 1) {
self.marketingNotice = ''
} else {
self.marketingNotice = ''
}
} else {
self.showNotice = false
}
self.compList = result.compDataList.filter(self.getCompList)
/*  */
self.activityBaseInfo = self.normalComp.data.activityBaseInfo
if (self.activityBaseInfo.activityMusic) {
self.backgroundMusic = self.getAudioFromQiniu(self.activityBaseInfo.activityMusic);
}

let compnents = result.compDataList
if (compnents && compnents.length > 0) {
let navHeaderAAComp = compnents.find((comp) => {
return comp.code === 'NavHeaderAA'
})
self.footer_01.data.footerMenus[0].link = navHeaderAAComp.data.shopHome
self.footer_01.data.footerMenus[1].link = 'tel://' + navHeaderAAComp.data.hotLine
if (self.activityBaseInfo.redBagEnable === 1) {
let tmpFooterMenu = {
code: '',
title: 'TA',
link: '',
icon: '/static/img/babyShow/hongbao.svg'
}
self.footer_01.data.footerMenus.push(tmpFooterMenu)
console.log(self.footer_01.data)
}
}

if (!!self.normalComp.data.pageImgList && self.normalComp.data.pageImgList.length > 0) {
/*  */
let tmpImgList = self.normalComp.data.pageImgList.filter(function (obj) {
return obj.label.indexOf('BASE') >= 0
})
console.log('')
self.baseImgs = self.getRealImg(tmpImgList)
/*  */
let tmpBabyShowImgs = self.normalComp.data.pageImgList.filter(function (obj) {
return obj.label.indexOf('BABY_SHOW') >= 0
})
console.log('')
self.babyShowImgs = self.getRealImg(tmpBabyShowImgs)
console.log(self.babyShowImgs)
/*  */
let tmpGiftGuideImgs = self.normalComp.data.pageImgList.filter(function (obj) {
return obj.label.indexOf('GIFT_GUIDE') >= 0
})
console.log('')




self.giftGuideImgs = self.getRealImg(tmpGiftGuideImgs)
self.giftGuideImgs[2] = tmpGiftGuideImgs[2].value
/*  */
let tmpGiftReceiveImgs = self.normalComp.data.pageImgList.filter(function (obj) {
return obj.label.indexOf('GIFT_RECEIVE') >= 0
})
console.log('')
self.giftReceiveImgs = self.getRealImg(tmpGiftReceiveImgs)

console.log(self.giftReceiveImgs)
/*  */
let tmpStorePromotionImgs = self.normalComp.data.pageImgList.filter(function (obj) {
return obj.label.indexOf('STORE_PROMOTION') >= 0
})
console.log('')
self.storePromotionImgs = self.getRealImg(tmpStorePromotionImgs)
console.log(self.storePromotionImgs)
}


/*  */
self.activityRule.content = self.activityBaseInfo.activityRule

/*  */
self.activityGift = self.normalComp.data.activityGift
/*  */
console.log('')
self.activityRewardList = self.normalComp.data.activityRewardList
if (!self.activityRewardList) {
return
}
if (!!self.activityRewardList && self.activityRewardList.length > 0) {
let rewardImgWidth = parseInt(wd / 4, 10)
for (let i = self.activityRewardList.length - 1; i >= 0; i--) {
self.activityRewardList[i].img = self.getImageView(1, rewardImgWidth, rewardImgWidth, self.normalComp.data.activityRewardList[i].img)
self.activityRewardList[i].haveLoves = self.activityBaseInfo.haveLoves
let tmpCompletion = (self.activityRewardList[i].haveLoves / self.activityRewardList[i].condition) * 100
console.log(tmpCompletion)
if (tmpCompletion >= 100) {
self.activityRewardList[i].lockStatus = ''
self.activityRewardList[i].completion = 100
} else {
self.activityRewardList[i].lockStatus = ''
self.activityRewardList[i].completion = tmpCompletion
}
}
}
/*  */
self.barrageInfoList = self.normalComp.data.barrageInfoList
if (!self.barrageInfoList) {
self.playCount = 0
}
if (!!self.barrageInfoList && self.barrageInfoList.length > 0) {
self.playCount = self.barrageInfoList.length
console.log('xxx1')
let sessionTmp = 0
for (let j = 0; j < self.barrageInfoList.length; j++) {
if (!self.barrageInfoList[j].userAvatar) {
self.barrageInfoList[j].userAvatar = '/static/img/babyShow/1.png'
}
self.barrageInfoList[j].time = 2000 * j
let is2Top = (sessionTmp + 1) * 30
if (is2Top > 120) {
sessionTmp = 0
} else {
sessionTmp++
}
self.barrageInfoList[j].top = (sessionTmp + 1) * 30
}
console.log('')
console.log(self.barrageInfoList)
}
document.title = result.pageTitle
$('.pullUp').addClass('animated infinite slideInUp')
$('.pullDown').addClass('animated infinite bounce pulse')
self.isDownload = true
}
})
},
initBarrage() {

$('.switchBarrage').addClass('icon-kaidanmu animated infinite rotateAll').removeClass('icon-guandanmu')
let self = this
var director = $({})
console.log('')
console.log(self.barrageInfoList)
$.each(self.barrageInfoList, function (k, play) {

var session = k

play.session = session

var actor = new Actor(play)


director.on(session + 'start', function () {
actor.perform()
})
})
var currentSession = 0

setInterval(function () {

director.trigger(currentSession + 'start')


if (currentSession === self.playCount) {
currentSession = 0
} else {
currentSession++
}
}, self.CheckTime)
},
/*  */
getCompList(obj) {
if (obj.code === 'NORMAL') {
this.normalComp = obj
console.log(this.normalComp)
}
return obj.code !== 'NORMAL'
},
toggleBarrage() {
let self = this
self.showBarrage = !self.showBarrage
if (self.showBarrage) {
$('.switchBarrage').addClass('animated infinite rotateAll')
$('.switchBarrage').removeClass('icon-guandanmu').addClass('icon-kaidanmu')
} else {
$('.switchBarrage').removeClass('animated infinite rotateAll')
$('.switchBarrage').addClass('icon-guandanmu').removeClass('icon-kaidanmu')
}
},
audioAutoPlay() {
let audio = $('#audioTag').get(0)
audio.play()
$('#playPause').addClass('animated infinite rotateAll')
document.addEventListener("WeixinJSBridgeReady", function () {
audio.play()
}, false)
},
controlPlay() {

let audio = $('#audioTag').get(0)
if (audio.paused) {
audio.play()
$('#playPause').addClass('animated infinite rotateAll')
} else {
audio.pause()
$('#playPause').removeClass('animated infinite rotateAll')
}
},
animationAAStart() {
$('#middleImg').css('display', 'block').addClass('animated fadeInUp')
setTimeout(function () {
$('#littleImg').css('display', 'block')
$('#littleImg').addClass('animated fadeInUp')
}, 500)
setTimeout(function () {
$('#introduceText').css('display', 'block')
$('#introduceText').addClass('animated fadeInUp')
}, 1000)
setTimeout(function () {
$('#introduceImg').css('display', 'block')
$('#introduceImg').addClass('animated fadeInUp')
}, 1500)
setTimeout(function () {
$('#littleImg').removeClass('fadeInUp').addClass('animated tada infinite')
}, 1500)
clearTimeout()
},
animationBBStart() {
$('.littleImg, .introduceText, .introduceImg').css({
'display': 'none'
})
$('.middleImg').removeClass('animated infinite pulse')
$('#bagTitle').css('display', 'block').addClass('animated fadeInUp')
$('#red-bag-body').css('display', 'block').addClass('animated fadeInUp')
setTimeout(function () {
$('#red-bag-hand').css('display', 'block').addClass('animated infinite pulse')
}, 1000)
clearTimeout()
},
showActivityStateInfo(state) {
if (state === -1 || state === 1) {
let text = state === -1 ? '' : '';
this.$vux.toast.show({
position: 'middle',
type: 'warn',
text: text
})
}
},
setShowSendForm() {
if (this.activityState === 0) {
this.showSendForm = true;
} else {
this.showActivityStateInfo(this.activityState);
}
},
showGiftReceive() {
if (this.activityState === 0) {
this.showGetForm = true;
} else {
this.showActivityStateInfo(this.activityState);
}
},
addTmpBarrage(payload) {
let self = this
let sendRedBagPara = {
userAvatar: self.userInfo.avatar,
redBagNum: payload.curBagNum,
nickname: self.userInfo.nickname
}

self.activityBaseInfo.haveLoves += parseInt(sendRedBagPara.redBagNum);
if (!self.barrageInfoList) {
self.barrageInfoList = [];
}
self.barrageInfoList.push(sendRedBagPara);

/*  */
sendRedBagPara.time = 0
sendRedBagPara.top = Math.ceil(Math.random() * 150)
sendRedBagPara.duration = 10000
let actorTmp = new Actor(sendRedBagPara)
actorTmp.perform()
self.showSendForm = false
this.$vux.toast.show({
position: 'middle',
type: 'success',
text: ''
});
},
sendLove() {
let self = this;
let baseParam = self.getBaseParam(cookie);
this.showActivityStateInfo(self.activityState);
if (self.activityState === 0) {
if (!self.isSendLove) {
let ActivityBabyShowLoveGiveRequest = {
shopCode: baseParam.shopCode,
activityId: self.activityId
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_BABYSHOW_LOVE_GIVE), ActivityBabyShowLoveGiveRequest).then((res) => {
console.log(res)
if (res.body.code === 1) {
self.isSendLove = true;
self.activityBaseInfo.haveLoves += 1;
this.$vux.toast.show({
position: 'middle',
type: 'success',
text: ''
});
} else {
this.$vux.toast.show({
position: 'middle',
type: 'warn',
text: res.body.message
});
}
})
} else {
this.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
}
}
}
}
}



