
import NavHeaderAA from '@/marketing/common/NavHeaderAA'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
directives: {
TransferDom
},
data () {
return {
currentUser: {},
navHeaderAAComp: {
'code': '',
'show': '',
'data':{
'shopHome':'',
'shopQrcode':'',
'shopIcon':'',
'shopName':'',
'hotLine':'',
'shopMember':'',
'shopAddress':''
}
},
activityId: '',
activityState: '',
activityEnd: false,
marketingNotice: '',
showNotice: true,
startDate: '',
endDate: '',
startScreenImg: '',
musicIcon: '',
musicSrc: '',
ruleDesc: '',
ruleSkin: '/static/img/themeShow/activityRule.png',
themeInfo: {
type: '', // THEME_SPIKE|THEME_GROUP|...
name: '', // 
price: '', // Double
activityPriceDesc: '', // 
activityPrice: '', // Double
quantity: '', // Long
img: '', // {w}*{h}1M
detailImg: ''
},
hotLineInfo: {
avatar: "",
contact: "",
img: "",
name: ""
},
title: '',
titleBgImg: '',
promotion: '/static/img/themeShow/seckillTheme.png',
storePromotionImg: '',
showRuleDetail: false,
showContactPage: false,
showOrderPage: false,
showThemeDetail: false,
showSeckillTheme: false,
hotLineImg: '',
leftTimes: '',
countDown: {
leftDays: '',
leftHour: '',
leftMin: '',
leftSec: '',
leftMSec: ''
},

name: '',
contact: ''
}
},
components: {
NavHeaderAA,
XButton,
XInput,
Group,
Popup,
TransferDom
},
computed: {
pageCode() {
return this.$route.params.pageCode
},
from() {
return this.$route.params.p
},
r() {
return this.$route.query.r
},
p() {
return this.$route.query.p
},
themeScreenWidth () {
return this.getViewportSize().width
}
},
watch: {
leftTimes: function () {
let self = this

if (self.leftTimes <= 0) {
self.countDown.leftDays = 0
self.countDown.leftHour = 0
self.countDown.leftMin = 0
self.countDown.leftSec = 0
self.countDown.leftMSec = 0
$('.ma-theme-countDown').css('width', '85px')
self.activityEnd = true
} else {
self.countDown.leftDays = Math.floor(self.leftTimes / (24 * 3600 * 1000))
let left1 = self.leftTimes % (24 * 3600 * 1000)
self.countDown.leftHour = Math.floor(left1 / (3600 * 1000))
let left2 = left1 % (3600 * 1000)
self.countDown.leftMin = Math.floor(left2 / (60 * 1000))
let left3 = left2 % (60 * 1000)
self.countDown.leftSec = Math.round(left3 / 1000)
self.countDown.leftMSec = left3 % 1000
}




}
},
mounted() {
let self = this
self.getPageData()
/* DOMContentLoadedWeixinJSBridgeReady */
document.addEventListener('DOMContentLoaded', function () {
console.log('hahha')
self.audioAutoPlay()
})
},
methods: {
doWechatOperation() {
let baseParam = this.getBaseParam(cookie);
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode,
href: window.location.href
};
let reportRequest = {
shopCode: baseParam.shopCode,
sourceType: 'MT_ACTIVITY_MOM_SHOW',
sourceCode: this.activityId,
pageCode: this.pageCode,
parentUser: this.p ? this.p : '',
};
let bizParams = {
r: this.r,
p: this.currentUser.code // parent
}
this.setWxOperate(wxOperateForm, reportRequest, bizParams)
},
getPageData() {
console.log('')
let self = this
let baseParam = self.getBaseParam(cookie)
let ActivityPageDataRequest = {
shopCode: baseParam.shopCode,
pageCode: self.pageCode,
r: self.r,
p: self.p
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_THEMESHOW_PAGEDATA),ActivityPageDataRequest).then((res) => {
console.log(res)
if (res.body.code === 1) {
let result = res.body.data
self.currentUser = result.currentUser;
this.doWechatOperation();

self.activityId = result.activityId
self.activityState = result.activityState

self.navHeaderAAComp = result.navHeaderAAComp
let normalComp = result.normalComp
self.activityId = normalComp.id
self.activityState = normalComp.state
if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.showNotice = true
if (self.activityState === -1) {
self.marketingNotice = ''
} else if (self.activityState === 1) {
self.marketingNotice = ''
self.activityEnd = true
} else {
self.marketingNotice = ''
}
} else {
self.showNotice = false
}
self.startDate = normalComp.startDate
self.endDate = normalComp.endDate

self.startScreenImg = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, normalComp.bgImg)

self.storePromotionImg = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, normalComp.storePromotionImg)

self.musicIcon = self.getImgFromQiniu(normalComp.musicImg)

self.musicSrc = self.getAudioFromQiniu(normalComp.bgMusicKey)



self.hotLineImg = self.getImgFromQiniu(normalComp.hotLineImg)

self.ruleDesc = normalComp.ruleDesc


if (normalComp.bgType === 'COLOR') {
$('#theme-show').css({
'background': normalComp.bgVal
})
} else {
$('#theme-show').css({
'background-image': 'url(' + normalComp.bgVal + ')'
})
}
document.title = normalComp.title
self.title = normalComp.title
self.titleBgImg = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, normalComp.titleBgImg)
if (normalComp.themeInfo) {

self.themeInfo = normalComp.themeInfo
self.themeInfo.detailImg = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, self.themeInfo.detailImg)
self.themeInfo.img = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, self.themeInfo.img)
}
if (normalComp.hotLineInfo) {

self.hotLineInfo = normalComp.hotLineInfo
self.hotLineInfo.avatar = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, self.hotLineInfo.avatar)
self.hotLineInfo.img = self.getImageView(3, self.themeScreenWidth, self.themeScreenWidth, self.hotLineInfo.img)
} else {
self.hotLineInfo.contact = self.navHeaderAAComp.data.hotLine;
}


self.leftTimes = self.endDate - new Date().getTime()
let interval = setInterval(function (result) {
if (self.leftTimes <= 0) {
clearInterval(interval)
self.countDown.leftDays = 0
self.countDown.leftHour = 0
self.countDown.leftMin = 0
self.countDown.leftSec = 0
self.countDown.leftMSec = 0
$('.ma-theme-countDown').css('width', '85px')
self.activityEnd = true
} else {
self.leftTimes = self.endDate - new Date().getTime()
}
}, 1)

self.$emit('changeLoadState', true)
} else {
self.$vux.toast.text('')
}
})


},
audioAutoPlay() {
let audio = $('.ma-musicSrc').get(0)
$('.ma-ruleSkin').addClass('animated infinite swing')
$('.ma-hotline').addClass('animated infinite tada')
document.addEventListener("WeixinJSBridgeReady", function () {
$('.ma-musicControl').addClass('animated infinite rotateAll')
audio.play()
}, false)
},
controlPlay() {

let audio = $('.ma-musicSrc').get(0)
if (audio.paused) {
audio.play()
$('.ma-musicControl').addClass('animated infinite rotateAll')
} else {
audio.pause()
$('.ma-musicControl').removeClass('animated infinite rotateAll')
}
},
viewMoreDetail () {
let self = this
let result
if (self.activityEnd) {
result = false
self.$vux.toast.text('')
} else {
result = true
}
return result
},
contactUs () {
let self = this
if (!self.isEmpty(self.hotLineInfo.contact)) {
window.location.href = 'tel://' + self.hotLineInfo.contact
} else {
window.location.href = 'tel://' + self.navHeaderAAComp.data.hotLine
}
},
confirmOrderInfo() {
let self = this
if(self.isEmpty(self.name)) {
self.$vux.toast.text('')
} else if (self.isEmpty(self.contact)){
self.$vux.toast.text('')
} else if (!self.isMobile(self.contact)) {
self.$vux.toast.text('')
} else {
self.submitOrderForm()
}
console.log('')
},
submitOrderForm() {
console.log('')
let self = this
console.log(self.isMobile(self.contact))
let reservationRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
name: self.name,
contact: self.contact,
r: self.r,
p: self.p
}
console.log(reservationRequest)
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_THEMESHOW_RESERVATION_SUBMIT), reservationRequest).then((res) => {
console.log(res)
if(res.body.code === 1) {
self.$vux.toast.text('', 'middle')
self.name = '';
self.contact = '';
self.showOrderPage = false
} else {
let msg = res.body.message ? res.body.message : '';
self.$vux.toast.text(msg, 'middle')
}
})
}
}
}



