import * as types from '../mutations-types'

const mutations = {
  [types.UPDATE_APPID](state, appId) {
    state.appId = appId
  },
  [types.UPDATE_USER_TOKEN](state, userToken) {
    state.userToken = userToken
  },

  [types.UPDATE_SHOPCODE](state, shopCode) {
    state.shopCode = shopCode
  },
  [types.UPDATE_SHOPTHEME](state, shopTheme) {
    state.shopTheme = shopTheme
  },
  [types.SET_USERSCORE](state, userScore) {
    // window.sessionStorage.setItem('userScore', JSON.stringify(userScore))
    state.userScore = JSON.parse(userScore)
  }
}

export default mutations



