
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data () {
return {
usableScore: this.$store.state.userScore.usableScore + 300,
list: this.$route.query.list
}
},
components: {
Group,
XButton
},
computed: {
scoreState: function () {
let str
if (this.list.scorePrice < this.usableScore) {
str = ''
} else {
str = ''
}
return str
}

},
methods: {
onChange (val) {
console.log(val)
},
gotodo (val) {
let self = this
self.$vux.confirm.show({
title: '',
content: '' + val + '',
confirmText: '',
cancelText: '',
onCancel () {
console.log('cancel')
},
onConfirm () {
console.log('')
self.$router.push({path: '/scoreShop'})
}
})
}
}
}



