
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
props: ['compData'],
data () {
return {
userSelf: '',
userRankingList: []
}
},
components: {
ViewBox
},
computed: {
userRank: function () {
let result
let self = this
if (self.userSelf.ranking) {
result = self.userSelf.ranking
} else {
result = ''
}
return result
},
userFriensNum: function () {
let res
let self = this
if (self.userSelf.friendsNum) {
res = self.userSelf.friendsNum
} else {
res = 0
}
return res
},
viewAssitList: function () {
let result
let self = this
if (self.userRankingList && self.userRankingList.length > 0) {
result = true
} else {
result = false
}
return result
}
},
mounted () {
this.initPageData()
},
methods: {
initPageData () {
let self = this
if (!self.compData) {
return
}
if (self.compData) {
self.userSelf = self.compData.currentUserRanking
self.userRankingList = self.compData.userRankingList
}
console.log(self.userSelf)
console.log(self.userRankingList)
}
}
}



