
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
showAlert: false,
alertText: null,
payType: 2, // 
gotoOrders: false,


endTime: this.$route.query.endTime,
WxPayUnifiedOrderRequest: JSON.parse(this.$route.query.WxPayUnifiedOrderRequest)
}
},
components: {
Group,
Cell,
XButton
},
created () {
},
mounted () {
},
beforeDestroy () {
},
props: [],
computed: {
},
methods: {
confrimPay () {
let self = this
if (this.payType === 1) {
console.log('')
} else {
console.log('')
if (self.isWechatBrowser()) {
let baseParam = self.getBaseParam(cookie)
let WechatSignatureForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
href: window.location.href
}
self.$http.post(self.addAPIVersion(self.WtConst.API_SIGNATURE_JSAPI_GET), WechatSignatureForm).then((res1) => {

if (res1.body.code === 1) {
self.signature = res1.body.data
console.log('signature:{}', JSON.stringify(self.signature))
this.$wechat.config({
debug: false,
appId: self.signature.appId,
timestamp: self.signature.timest,
nonceStr: self.signature.nonceStr,
signature: self.signature.signature,
jsApiList: [
'chooseWXPay'
]
})
let WxPayUnifiedOrderRequest = self.WxPayUnifiedOrderRequest
WxPayUnifiedOrderRequest.tradeType = self.isWechatBrowser() ? 'JSAPI' : 'MWEB'

self.$http.post(self.WtConst.API_PLUGIN_WX_PAY_UNIFIED_ORDER, JSON.stringify(WxPayUnifiedOrderRequest)).then((result) => {
if (result.body.code === 1) {

let wxPayUnifiedOrderResult = result.body.data
console.log(wxPayUnifiedOrderResult)
if (self.isWechatBrowser()) {
console.log('1:' + wxPayUnifiedOrderResult.mwebUrl)
self.$wechat.ready(function () {
self.$wechat.chooseWXPay({
appId: wxPayUnifiedOrderResult.appId,
timestamp: wxPayUnifiedOrderResult.timeStamp,
nonceStr: wxPayUnifiedOrderResult.nonceStr,
package: wxPayUnifiedOrderResult.pkg,
signType: wxPayUnifiedOrderResult.signType,
paySign: wxPayUnifiedOrderResult.paySign,




success: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.success: ' + JSON.stringify(res))
self.$router.push({name: 'orderList'})
},
cancel: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.cancel: ' + JSON.stringify(res))
},
fail: function (res) {
console.log('editOrderInfo.vue, chooseWXPay.fail: ' + JSON.stringify(res))
self.$vux.alert.show({
content: ''
})

}
})
self.$wechat.error(function (res) {
console.log('editOrderInfo.vue, error: ' + JSON.stringify(res))
})
})
} else {
console.log('H5')
window.location.href = wxPayUnifiedOrderResult.mwebUrl
}
} else {
console.log('-{}', result.body.message)
self.$router.push({name: 'orderList'})
}
})
} else {

}
})
} else {
console.log('')
}
}
},
closeTipFun () {
}
}
}



