
import PaletteButton from '@/components/common/palette-button'
import FooterNav from '@/components/common/FooterNav'
import Divider from 'vux/src/components/divider/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
show1: true,
couponList: []
}
},
components: {
PaletteButton,
FooterNav,
Divider,
Cell,
XButton,
Loading
},
computed: {
hasCoupon: function () {
let self = this
let res
if (!!self.couponList && self.couponList.length > 0) {
res = true
} else {
res = false
}
return res
}
},
created () {
this.getMyCouponList()
},
methods: {
getMyCouponList () {
let self = this
setTimeout(() => {
this.show1 = false
}, 1000)
console.log(self.setBaseForm())
this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_COUPON_MINE_LIST), self.setBaseForm(cookie)).then((res) => {
console.log(res)
if (res.body.code === 1) {
console.log(res.body.data)
self.couponList = res.body.data
} else {
self.$vux.toast.text('', 'middle')
}
})
},
getCoupon (index) {
let self = this
this.$vux.alert.show({
title: '',
onHide () {
console.log('1213')
self.$set(self.couponList[index], 'hasReceived', true)
console.log(self.couponList)
}
})
},
viewDetail (list) {
this.$router.push({name: 'myCouponDetail', query: { list }})
},
contactUs () {
window.location.href = 'tel://' + JSON.parse(window.sessionStorage.getItem('shopInfo')).telephone
}
}
}



