
import maThemeShow from '@/marketing/activity/maThemeShow/maThemeShow'
import maLiveShow from '@/marketing/activity/maLiveShow/maLiveShow'
import maMomShow from '@/marketing/activity/maMomShow/maMomShow'
import XCircle from 'vux/src/components/x-circle/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import '@/marketing/activity/activity.css'
export default {
data () {
return {
nonDownload: true,

loadingPercent: 0,
strokeColor2: '#AAA',

x: false
}
},
computed: {
screenHeight () {
return this.getViewportSize().height
},
pageCode () {
return this.$route.params.pageCode
},
comp () {
console.log('')
console.log(this.$route.query.comp)
return this.$route.query.comp
},
loadingLogo () {
let self = this
let tmpLogo = self.$route.query.loading
console.log('loadingLogo')
console.log(tmpLogo)
tmpLogo = self.getImgFromQiniu(tmpLogo)
console.log(tmpLogo)
return tmpLogo
}
},
beforeMount () {
this.preLoading()
},
components: {
maThemeShow,
maLiveShow,
maMomShow,
XCircle,
cookie
},
methods: {
preLoading () {
let self = this
/*  */
let intstart = setInterval(function () {
if (self.x) {
console.log('unpdateLoading')
clearInterval(intstart)
} else {
if (self.loadingPercent >= 30) {
clearInterval(intstart)
} else {
console.log('loadingPercentHHHH')
self.loadingPercent += 5
console.log('loadingPercentHHHH')
console.log(parseInt(self.loadingPercent, 10))
}
}
}, 50)
},
unpdateLoading (payload) {
let self = this
console.log('loadingPercentXXX')
console.log(payload)
self.x = true
/*  */
if (payload) {
console.log(self.loadingPercent)
if (self.loadingPercent >= 100) {
self.loadingPercent = 100
self.nonDownload = false
} else {
let intEnd = setInterval(function () {
if (self.loadingPercent >= 100) {
self.loadingPercent = 100
clearInterval(intEnd)
console.log('loadingPercentMMMM')
setTimeout(function () {
self.nonDownload = false
}, 500)
} else {
self.loadingPercent += 10
console.log('loadingPercent00')
console.log(parseInt(self.loadingPercent, 10))
}
}, 80)
}
} else {
self.$vux.toast.text('')
}
}
}
}



