
/* eslint-disable indent,no-extra-boolean-cast,no-underscore-dangle,padded-blocks,space-before-function-paren,fun-call-spacing,no-console,space-infix-ops */
import PaletteButton from '@/components/common/palette-button'
import FooterNav from '@/components/common/FooterNav'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'


export default {
name: 'home',
data () {
return {
showFocus: false,
isShowHot: false,
shopInfo: {},
mainModuleList: [],
navModuleList: []
}
},
computed: {
moduleWidth: function () {
let res
if (this.mainModuleList.length <= 4) {
res = '50%'
} else if (this.mainModuleList.length > 4 && this.mainModuleList.length <= 6) {
res = '33%'
} else {
res = '25%'
}
return res
}
},
components: {
FooterNav,
PaletteButton,
Swiper,
SwiperItem
},
created () {
this.initBaseShopInfo()
console.log('Home-created-shopInfo.shopName:' + JSON.stringify(this.shopInfo.name))
},
watch: {
'$route': 'initBaseShopInfo'
},
mounted () {

let baseParam = this.getBaseParam(cookie)
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: 'HOME',
href: window.location.href
}
this.setWxOperate(wxOperateForm)
},
methods: {
initBaseShopInfo () {
let self = this
let baseParam = self.getBaseParam(cookie)
console.log('baseParam:' + JSON.stringify(baseParam))

let baseShopInfo = self.getBaseShopInfoFromSessionStorage()
let isNeedToRefreshBaseShopInfo = (self.isEmpty(baseShopInfo) ||
self.isEmpty(baseShopInfo.shopInfo) || self.isEmpty(baseShopInfo.mainModuleList) || self.isEmpty(baseShopInfo.navModuleList) ||
(baseParam.appId !== baseShopInfo.shopInfo.appId && baseParam.shopCode !== baseShopInfo.shopInfo.shopCode))
console.log('isNeedToRefreshBaseShopInfo:' + isNeedToRefreshBaseShopInfo)
if (isNeedToRefreshBaseShopInfo) {
console.log('refresh base shop info.')
self.$http.post(self.addAPIVersion(self.WtConst.API_SHOP_MAIN_PAGEDATA), {appId: baseParam.appId, shopCode: baseParam.shopCode}).then((res) => {
if (res.body.code === 1) {

let tmpResult = self.formatBaseShopInfo(res.body.data)
self.shopInfo = tmpResult.shopInfo
self.mainModuleList = tmpResult.mainModuleList
self.navModuleList = tmpResult.navModuleList
} else {

console.error('base shop info query fail.')
return
}
})
} else {
self.shopInfo = baseShopInfo.shopInfo
self.mainModuleList = baseShopInfo.mainModuleList
self.navModuleList = baseShopInfo.navModuleList
console.log('shopInfo.shopName:'+JSON.stringify(self.shopInfo.name))
}
},
toggleHot () {
if (this.isShowHot) {
this.isShowHot = false
} else {
this.isShowHot = true
}
},
main_log (val) {
console.log('main_log', val)
},
sub_log (val) {
console.log('sub_log', val)
this.$refs.target_1.collapse()
},
contactUs () {
window.location.href = 'tel://' + this.shopInfo.telephone
}
}
}



