/* eslint camelcase: "error" */
/* eslint dot-notation: ["error", { "allowPattern": "^[a-z]+(_[a-z]+)+$" }] */
/* eslint no-underscore-dangle: "error" */
/* eslint no-unused-vars: "error" */

export default {
  name: 'mt-palette-button',

  data: function () {
    return {
      transforming: false,    // 
      expanded: false           // 
    }
  },

  props: {
    content: {
      type: String,
      default: ''
    },

    offset: {
      type: Number,           // lt
      default: Math.PI / 4
    },

    direction: {
      type: String,
      default: 'lt'           // lt t rt this.radius rb b lb l 8
    },

    radius: {
      type: Number,
      default: 90
    },

    mainButtonStyle: {
      type: String,           //  mint-main-button  class
      default: ''
    }
  },
  methods: {
    toggle (event) {
      if (!this.transforming) {
        if (this.expanded) {
          this.collapse(event)
        } else {
          this.expand(event)
        }
      }
    },
    onMainAnimationEnd () {
      this.transforming = false
      this.$emit('expanded')
    },

    expand (event) {
      this.expanded = true
      this.transforming = true
      this.$emit('expand', event)
    },

    collapse (event) {
      this.expanded = false
      this.$emit('collapse', event)
    }
  },
  mounted () {
    this.slotChildren = []
    for (let i = 0; i < this.$slots.default.length; i++) {
      if (this.$slots.default[i].elm.nodeType !== 3) {
        this.slotChildren.push(this.$slots.default[i])
      }
    }
    let css = ''
    let directionArc = Math.PI * (3 + Math.max(['lt', 't', 'rt', 'r', 'rb', 'b', 'lb', 'l'].indexOf(this.direction), 0)) / 4
    for (let i = 0; i < this.slotChildren.length; i++) {
      var arc = (Math.PI - this.offset * 2) / (this.slotChildren.length - 1) * i + this.offset + directionArc
      var x = (Math.cos(arc) * this.radius).toFixed(2)
      var y = (Math.sin(arc) * this.radius).toFixed(2)
      var itemCss = '.expand .palette-button-' + this.uId + '-sub-' + i + '{transform:translate(' + x + 'px,' + y + 'px) rotate(720deg);transition-delay:' + 0.03 * i + 's}'
      css += itemCss
      this.slotChildren[i].elm.className += (' palette-button-' + this.uId + '-sub-' + i)
    }

    this.styleNode = document.createElement('style')
    this.styleNode.type = 'text/css'
    this.styleNode.rel = 'stylesheet'
    this.styleNode.title = 'palette button style'
    this.styleNode.appendChild(document.createTextNode(css))
    document.getElementsByTagName('head')[0].appendChild(this.styleNode)
  },

  destroyed () {
    if (this.styleNode) {
      this.styleNode.parentNode.removeChild(this.styleNode)
    }
  }
}



