
import NavHeaderAA from '@/marketing/common/NavHeaderAA'
import ActivityCommodityBB from '@/marketing/common/ActivityCommodityBB'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import SlideBarrage from './slideBarrage.js'
import StretchBarrage from './stretchBarrage.js'
export default {
directives: {
TransferDom
},
data () {
return {
showWarning: false,
navHeader: {
'code':'NavHeaderAA',
'show':true,
'data':{
'shopHome':'',
'shopQrcode':'',
'shopIcon':'',
'shopName':'',
'hotLine':'',
'shopMember':''
}
},
currentUser: {},
activityId: '',
activityState: '',
marketingNotice: '',
showNotice: true,
startDate: '',
endDate: '',
talsStartScreenBgImg: '',
musicIcon: '',
musicSrc: '',
luckyBagImg: '',
barrageIcon: '',
talsBarrageIcon: '',
closeBarrage: false,
giver: {
name: '',
avator: '/static/img/liveShow/hostAvator.png'
},
hotLineInfo: {},
blessingScreenPersonImg: '',
blessingScreenDesc: '',
liveShowGivers: [],
giftGiversUnique: [],
playCount: '',
totalStageProps: 0,
totalAgrees: 0,
unlockGifts: [],
activityPromotionScreenTitle: '',
activityPromotionScreenImg: '',
rewardUnlockScreenTitle: '',
footerNav: [
{ icon: '/static/img/liveShow/reservation.png', title: '' },
{ icon: '/static/img/liveShow/sendGift.png', title: '' },
{ icon: '/static/img/liveShow/hotLine.png', title: '' }
],


showSendGift: false,
showContactPage: false,
showReceiveGift: false,
showOrderForm: false,
showBarrage: false,
luckyBagGift: {
name: '', // 
img: '', // {w}*{h}1M
price: '', // 
numLeft: '', // 0
},
fortuneForm: {
babyName: '',
contact: '',
parentName: '',
babyBirthday: ''
},
activityOrderForm: {
babyName: '',
contact: '',
parentName: '',
babyBirthday: ''
},
storePromotionScreenImg: '',
moneyScale: 2,
stageProps: [],
curSelectGift: {},
curSelecNumber: 1
}
},
computed: {
isHotInfo: function () {
let self = this
let hotState
if(self.isEmpty(self.hotLineInfo.img)) {
hotState = false
} else {
hotState = true
}
return hotState
},
menuWidth: function () {
let self = this
let res
if (self.footerNav.length > 5) {
res = 1 / 3 * 100 + '%'
} else {
res = 1 / self.footerNav.length * 100 + '%'
}

return res
},
pageCode() {
return this.$route.params.pageCode
},
r() {
return this.$route.query.r
},
p() {
return this.$route.query.p
},
screenWidth() {
return this.getViewportSize().width
}
},
components: {
NavHeaderAA,
ActivityCommodityBB,
Group,
XInput,
XButton,
Clocker,
Grid,
GridItem,
ViewBox,
Divider,
Popup,
TransferDom,
Flexbox,
FlexboxItem,
Checker,
CheckerItem,
XNumber,
cookie
},
watch: {
liveShowGivers: function () {
let self = this
self.giftGiversUnique = self.unique(self.liveShowGivers)
self.playCount = self.liveShowGivers.length
let sessionTmp = 0
for (let j = 0; j < self.liveShowGivers.length; j++) {
self.liveShowGivers[j].time = 2000 * j
let is2Top = (sessionTmp + 1) * 30
if (is2Top > 150) {
sessionTmp = 0
} else {
sessionTmp++
}
self.liveShowGivers[j].top = (sessionTmp + 1) * 30
}
self.initBarrage()
}
},
beforeMount () {
},
mounted () {
let self = this
self.getPageData()
/* DOMContentLoadedWeixinJSBridgeReady */
document.addEventListener('DOMContentLoaded', function () {
self.audioAutoPlay()
})
},
methods: {
doWechatOperation() {
let baseParam = this.getBaseParam(cookie);
let wxOperateForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
pageCode: this.pageCode,
href: window.location.href
};
let reportRequest = {
shopCode: baseParam.shopCode,
sourceType: 'MT_ACTIVITY_MOM_SHOW',
sourceCode: this.activityId,
pageCode: this.pageCode,
parentUser: this.p ? this.p : '',
};
let bizParams = {
r: this.r,
p: this.currentUser.code // parent
}
this.setWxOperate(wxOperateForm, reportRequest, bizParams)
},
getPageData () {
let self = this
let baseParam = self.getBaseParam(cookie)
let ActivityPageDataRequest = {
shopCode: baseParam.shopCode,
pageCode: self.pageCode,
r: self.r,
p: self.p
}
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_LIVESHOW_PAGEDATA),ActivityPageDataRequest).then((res) => {
if (res.body.code === 1) {
let result = res.body.data;
console.log(result)
self.currentUser = result.currentUser;
this.doWechatOperation();

self.navHeader = result.navHeaderAAComp;
self.navHeader.data.shopIcon = self.getImageView(3, self.screenWidth, self.screenWidth, self.navHeader.data.shopIcon);
self.navHeader.data.shopQrcode = self.getImageView(3, self.screenWidth, self.screenWidth, self.navHeader.data.shopQrcode);
let normalComp = result.normalComp;

self.activityId = normalComp.id;
self.activityState = normalComp.state;
self.startDate = normalComp.startDate;
self.endDate = self.formatDate(normalComp.endDate, 'yyyy-MM-dd HH:mm:ss');

if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.showNotice = true;
self.marketingNotice = self.getMarketingNotice(self.activityState);
} else {
self.showNotice = false;
}

self.luckyBagImg = self.getImgFromQiniu(normalComp.luckyBagImg);
normalComp.barrageIcon = normalComp.barrageIcon ? normalComp.barrageIcon.split(',') : null;
if (!!normalComp.barrageIcon && normalComp.barrageIcon.length > 0) {
for (let tmpBarr = 0; tmpBarr < normalComp.barrageIcon.length; tmpBarr++) {
normalComp.barrageIcon[tmpBarr] = self.getImgFromQiniu(normalComp.barrageIcon[tmpBarr])
}
self.barrageIcon = normalComp.barrageIcon
}
self.talsBarrageIcon = self.barrageIcon[0]

self.musicIcon = self.getImgFromQiniu(normalComp.musicImg)

self.musicSrc = self.getAudioFromQiniu(normalComp.bgMusicKey)

if (!!normalComp.hotLine && !self.isEmpty(normalComp.hotLine)) {
normalComp.hotLine.img = self.getImgFromQiniu(normalComp.hotLine.img);
normalComp.hotLine.avator = self.getImgFromQiniu(normalComp.hotLine.avator);
self.hotLineInfo = normalComp.hotLine;
}

if (normalComp.bgType === 'COLOR') {
$('#maLiveShow').css({
'background': normalComp.bgVal
});
} else {
$('#maLiveShow').css({
'background-image': 'url(' + normalComp.bgVal + ')'
});
}

document.title = normalComp.title;

self.talsStartScreenBgImg = self.getImageView(3, self.screenWidth, self.screenWidth, normalComp.bgImg);



if (normalComp.luckyBagGift) {
self.luckyBagGift = normalComp.luckyBagGift;

self.luckyBagGift.img = self.getImgFromQiniu(self.luckyBagGift.img)
}

self.blessingScreenDesc = normalComp.blessingScreenDesc;
self.blessingScreenPersonImg = self.getImageView(3, 375, 375, normalComp.blessingScreenPersonImg);

self.totalStageProps = normalComp.totalStageProps;
self.totalAgrees = normalComp.totalAgrees;
self.rewardUnlockScreenTitle = normalComp.rewardUnlockScreenTitle;

if (!!normalComp.unlockGifts && normalComp.unlockGifts.length > 0) {
for (let i = normalComp.unlockGifts.length - 1; i >= 0; i--) {
normalComp.unlockGifts[i].img = self.getImageView(3, 375, 375, normalComp.unlockGifts[i].img);
}
}
self.unlockGifts = normalComp.unlockGifts;

self.activityPromotionScreenTitle = normalComp.activityPromotionScreenTitle;
self.activityPromotionScreenImg = self.getImageView(3, self.screenWidth, self.screenWidth, normalComp.activityPromotionScreenImg);

self.storePromotionScreenImg = self.getImageView(3, self.screenWidth, self.screenWidth, normalComp.storePromotionScreenImg);

if(!!normalComp.stageProps && normalComp.stageProps.length > 0) {
for (let j = 0; j < normalComp.stageProps.length; j ++) {
if (!!normalComp.stageProps[j].firstKey) {
normalComp.stageProps[j].firstKey = self.getImgFromQiniu(normalComp.stageProps[j].firstKey);
}
if (!!normalComp.stageProps[j].secondKey) {
normalComp.stageProps[j].secondKey = self.getImgFromQiniu(normalComp.stageProps[j].secondKey);
}
}
}
self.stageProps = normalComp.stageProps;
if (!!normalComp.barrageList && normalComp.barrageList.length > 0) {
for (let bar = 0; bar < normalComp.barrageList.length; bar ++) {
if (normalComp.barrageList[bar].userAvatar && normalComp.barrageList[bar].userAvatar.indexOf('http') < 0) {
normalComp.barrageList[bar].userAvatar = self.getImgFromQiniu(normalComp.barrageList[bar].userAvatar);
}
if (normalComp.barrageList[bar].spFirstKey && normalComp.barrageList[bar].spFirstKey.indexOf('http') < 0) {
normalComp.barrageList[bar].spFirstKey = self.getImgFromQiniu(normalComp.barrageList[bar].spFirstKey);
}
if (normalComp.barrageList[bar].spSecondKey && normalComp.barrageList[bar].spSecondKey.indexOf('http') < 0) {
normalComp.barrageList[bar].spSecondKey = self.getImgFromQiniu(normalComp.barrageList[bar].spSecondKey);
}
}
self.liveShowGivers = normalComp.barrageList.slice(0, normalComp.barrageList.length);
self.giftGiversUnique = self.unique(self.liveShowGivers)
}

self.$emit('changeLoadState', true)

$('.ma-fortuneBag').addClass('animated infinite tada')
} else {
self.$vux.toast.text('')
}
})
},
initBarrage() {

let self = this
self.showBarrage = true
self.talsBarrageIcon = self.barrageIcon[0]
let director = $({})
$.each(self.liveShowGivers, function (k, item) {

let session = k
item.session = session
let slideBarrage = new SlideBarrage(item)


director.on(session + 'slideStart', function () {
slideBarrage.perform()
})



})
let currentSession = 0
let slideInt = setInterval(function () {

director.trigger(currentSession + 'slideStart')


if (currentSession === self.playCount) {

currentSession = 0
} else {
currentSession++
}
}, 4000)













},
audioAutoPlay () {
let audio = $('.ma-musicSrc').get(0)


document.addEventListener("WeixinJSBridgeReady", function () {

audio.play()
$('.ma-musicControl').addClass('animated infinite rotateAll')
}, false)
},
controlPlay () {

let audio = $('.ma-musicSrc').get(0)

if (audio.paused) {
audio.play()
$('.ma-musicControl').addClass('animated infinite rotateAll')
} else {
audio.pause()
$('.ma-musicControl').removeClass('animated infinite rotateAll')
}
},
getMarketingNotice (id) {
let tmpNotice
if (id === -1) {
tmpNotice = ''
} else if (id === 1) {
tmpNotice = ''
} else if (id === 2) {
tmpNotice = ''
} else {
tmpNotice = ''
}
return tmpNotice
},
changeBarrageState () {
let self = this
self.showBarrage = !self.showBarrage
if (self.showBarrage) {
self.talsBarrageIcon = self.barrageIcon[0]
} else {
self.talsBarrageIcon = self.barrageIcon[1]
}
},
doMenuMethods (index) {
let self = this

if (self.footerNav[index].title === '') {
self.contactUs()
} else if (self.footerNav[index].title === '') {
if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.$vux.toast.text(self.getMarketingNotice(self.activityState))
} else {
self.showOrderForm = true
}
} else if (self.footerNav[index].title === '') {
if (self.activityState === -1 || self.activityState === 1 || self.activityState === 2) {
self.$vux.toast.text(self.getMarketingNotice(self.activityState))
} else if (!self.stageProps || self.stageProps.length <= 0){
self.$vux.toast.text('')
} else {
self.showSendGift = true
}
} else {

}
},
showFortuneBag () {
let self = this
if (self.activityState === -1) {
self.$vux.toast.text('')
} else if (self.activityState === 1) {
self.$vux.toast.text('')
} else if (self.activityState === 2) {
self.$vux.toast.text('')
} else {
self.showReceiveGift = true
}
},
contactUs () {
let self = this
if (!self.isEmpty(self.hotLineInfo.contact)) {
window.location.href = 'tel://' + self.hotLineInfo.contact
} else {
window.location.href = 'tel://' + self.navHeader.data.hotLine
}
},
unique (arr) {
let result = {};
let finalResult = [];
for (let i = 0;i < arr.length;i ++){
result[arr[i].userCode] = arr[i]
}
for (let item in result){
finalResult.push(result[item])
}
return finalResult
},
confirmInfo(obj) {
let self = this
let tmpResult = false
if(self.isEmpty(obj.babyName)) {
self.$vux.toast.text('')
} else if (self.isEmpty(obj.contact)) {
self.$vux.toast.text('')
} else if (!self.isMobile(obj.contact)) {
self.$vux.toast.text('')
} else if (self.isEmpty(obj.parentName)) {
self.$vux.toast.text('')
} else if (self.isEmpty(obj.babyBirthday)) {
self.$vux.toast.text('')
} else {
tmpResult = true
}
return tmpResult
},
pushGiftReceiveForm() {
let self = this
if(self.confirmInfo(self.fortuneForm)) {
let activityLiveShowGiftReceiveRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
babyName: self.fortuneForm.babyName,
contact: self.fortuneForm.contact,
parentName: self.fortuneForm.parentName,
babyBirthday: self.fortuneForm.babyBirthday,
r: self.r,
p: self.p
}

self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_LIVESHOW_FUBAO_RECEIVE), activityLiveShowGiftReceiveRequest).then((res) => {

if (res.body.code === 1) {
self.$vux.toast.text('', 'middle');

self.fortuneForm.babyName = '';
self.fortuneForm.contact = '';
self.fortuneForm.parentName = '';
self.fortuneForm.babyBirthday = '';
self.showReceiveGift = false
} else {
let msg = res.body.message ? res.body.message : '';
self.$vux.toast.text(msg, 'middle')
}
})
}
},
pushOrderForm () {
let self = this
if(self.confirmInfo(self.activityOrderForm)) {
let activityLiveShowOrderRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
babyName: self.activityOrderForm.babyName,
contact: self.activityOrderForm.contact,
parentName: self.activityOrderForm.parentName,
babyBirthday: self.activityOrderForm.babyBirthday,
r: self.r,
p: self.p
}

self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_LIVESHOW_RESERVATION), activityLiveShowOrderRequest).then((res) => {

if (res.body.code === 1) {
self.$vux.toast.text('', 'middle');

self.activityOrderForm.babyName = '';
self.activityOrderForm.contact = '';
self.activityOrderForm.parentName = '';
self.activityOrderForm.babyBirthday = '';
self.showOrderForm = false
} else {
let msg = res.body.message ? res.body.message : '';
self.$vux.toast.text(msg, 'middle')
}
})
}
},
onFocus (str ,val, $event) {


let self = this
/*  */
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM-DD',
value: '2017-05-20',
onConfirm (value) {

if (str === 'receive') {
self.fortuneForm.babyBirthday = value
} else if (str === 'order') {

self.activityOrderForm.babyBirthday = value
} else {
}
},
onShow () {
},
onHide () {
}
})
},
viewPrice (number) {
let result
if (number === 0) {
result = ''
} else {
result = number
}
return result
},
handleSuccessPay(singleBarrage, num, item, wxPayReq) {
let stretchBarrage = new StretchBarrage(singleBarrage)
setTimeout(function () {
stretchBarrage.perform()
}, 1000)
this.showSendGift = false;
this.$vux.toast.text(wxPayReq.body);
this.liveShowGivers.push(singleBarrage);
this.totalStageProps += num;
this.totalAgrees += num * item.scale;
},
sendGift (item,num) {
let self = this
if (self.isEmpty(item.code)) {
self.$vux.toast.text('')
} else {
let activtityLiveShowStagePropRequest = {
shopCode: self.getBaseParam(cookie).shopCode,
activityId: self.activityId,
quantity: num,
stagePropCode: item.code,
r: self.r,
p: self.p
};
self.$http.post(self.addAPIVersion(self.WtConst.API_MARKETING_ACTIVITY_LIVESHOW_STAGEPROP_PAY), activtityLiveShowStagePropRequest).then((res) => {

if (res.body.code === 1) {
let payResult = res.body.data;
let singleBarrage = {
'id': self.liveShowGivers.length,
'quantity': num,
'userCode': self.currentUser.code,
'nickname': self.currentUser.nickname,
'userAvatar': self.currentUser.avatar,
'spName': item.name,
'spFirstKey': item.firstKey,
'spSecondKey': item.secondKey,
'spPrice': item.price,
'spScale': item.scale
};
if (payResult.totalFee <= 0) {

self.handleSuccessPay(singleBarrage, num, item, payResult);
} else {
self.payUnified(singleBarrage, num, item, payResult)
}
} else {
self.$vux.toast.text('')
}
})
}

},

payUnified (singleBarrage, num, item, wxPayReq) {
let self = this
let WxPayUnifiedOrderRequest = {
'appId': wxPayReq.appId,
'attach': wxPayReq.attach,
'body': wxPayReq.body,
'outTradeNo': wxPayReq.outTradeNo,
'totalFee': wxPayReq.totalFee
}
let baseParam = self.getBaseParam(cookie)

if (self.isWechatBrowser()) {
let WechatSignatureForm = {
appId: baseParam.appId,
shopCode: baseParam.shopCode,
href: window.location.href
}
self.$http.post(self.addAPIVersion(self.WtConst.API_SIGNATURE_JSAPI_GET), WechatSignatureForm).then((tmpRes) => {

if (tmpRes.body.code === 1) {
self.signature = tmpRes.body.data

this.$wechat.config({
debug: false,
appId: self.signature.appId,
timestamp: self.signature.timest,
nonceStr: self.signature.nonceStr,
signature: self.signature.signature,
jsApiList: [
'chooseWXPay'
]
})
WxPayUnifiedOrderRequest.tradeType = self.isWechatBrowser() ? 'JSAPI' : 'MWEB'

self.$http.post(self.WtConst.API_PLUGIN_WX_PAY_UNIFIED_ORDER, JSON.stringify(WxPayUnifiedOrderRequest)).then((result1) => {
if (result1.body.code === 1) {
let wxPayUnifiedOrderResult = result1.body.data

if (self.isWechatBrowser()) {
self.$wechat.ready(function () {
self.$wechat.chooseWXPay({
appId: wxPayUnifiedOrderResult.appId,
timestamp: wxPayUnifiedOrderResult.timeStamp,
nonceStr: wxPayUnifiedOrderResult.nonceStr,
package: wxPayUnifiedOrderResult.pkg,
signType: wxPayUnifiedOrderResult.signType,
paySign: wxPayUnifiedOrderResult.paySign,
success: function (res2) {
self.handleSuccessPay(singleBarrage, num, item, wxPayReq);
},
cancel: function (res2) {
},
fail: function (res2) {

self.$vux.toast.show({
position: 'middle',
type: 'warn',
text: ''
});
}
})
self.$wechat.error(function (res2) {

})
})
} else {

window.location.href = wxPayUnifiedOrderResult.mwebUrl
}
} else {

















}
})
} else {

}
})
} else {

}
}
}
}



