
import FooterNav from '@/components/common/FooterNav'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {

isDetail: true,
scoreDetailList: [




]
}
},
components: {
FooterNav,
Tab,
TabItem,
Group,
Cell
},
computed: {
userScore: function () {
return this.$store.state.userScore
}
},
created () {
this.getScoreDetail()
},
mounted () {

},
methods: {
getScoreDetail () {
let self = this

this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_RECORD_MINE_LIST), self.setBaseForm(cookie)).then((res) => {
console.log(res)
if (res.body.code === 1) {
let mainPageData = res.body.data
console.log(mainPageData)
let tmpScore = {}
if (mainPageData.userScore) {
console.log('ohk')
tmpScore = mainPageData.userScore
} else {
tmpScore.score = 0
tmpScore.frozenScore = 0
console.log('none')
}

this.$store.dispatch('setUserScore', JSON.stringify(tmpScore))
self.scoreDetailList = mainPageData.scoreRecordList
if (!self.scoreDetailList) {
return
}
if (!!self.scoreDetailList && self.scoreDetailList.length > 0) {
for (let i = 0; i < self.scoreDetailList.length; i++) {
self.scoreDetailList[i].createTime = self.formatDate(self.scoreDetailList[i].createTime, 'yyyy-MM-dd HH:mm:ss')
}
}
}
})
}
}
}



