
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
props: ['compData'],
data () {
return {
bannerImg: ''
}
},
components: {
Swiper,
SwiperItem
},
mounted () {
this.initPage()
},
methods: {
initPage () {
let self = this
let curWidth = self.getViewportSize().width
self.bannerImg = self.compData
self.bannerImg = !self.bannerImg ? '' : self.getImageView(3, curWidth, 200, self.bannerImg)
console.log(self.bannerImg)
},
clickAction (list) {
if (list.url) {
window.location.href = list.url
} else if (list.getApi) {
console.log(list.getApi)
console.log('API')
}
}
}
}



