
import FooterNav from '@/components/common/FooterNav'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data () {
return {
recordLists: [],
hasRecord: '',
show1: true
}
},
components: {
FooterNav,
Group,
Cell,
XButton,
Loading
},
computed: {
},
created () {
this.getExchangeRecode()
},
mounted () {
console.log(this.$store.state.userScore)
},
methods: {
getExchangeRecode () {
let self = this
setTimeout(() => {
this.show1 = false
}, 500)

this.$http.post(self.addAPIVersion(self.WtConst.API_SCORE_RECORD_MINE_LIST), self.setBaseForm(cookie)).then((res) => {
console.log(res)
if (res.body.code === 1) {
let mainPageData = res.body.data
console.log(mainPageData)
if (!mainPageData.scoreRecordList) {
return
}
if (!!mainPageData.scoreRecordList && mainPageData.scoreRecordList.length > 0) {
for (let i = 0; i < mainPageData.scoreRecordList.length; i++) {
if (mainPageData.scoreRecordList[i].status === 'OUT') {
self.recordLists.push(mainPageData.scoreRecordList[i])
}
}
}
if (!!self.recordLists && self.recordLists.length > 0) {
self.hasRecord = true
} else {
self.hasRecord = false
}
console.log(self.recordLists)
}
})
}
}
}



