
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data () {
return {
invitationCardImg: this.$route.query.invitationCardImg,
invitationCard: ''
}
},
components: {
XButton
},
computed: {
imgHeight: function () {
let self = this
return self.getViewportSize().height
}
},
mounted () {
this.getInvitationCardDetail()
},
methods: {
getInvitationCardDetail () {
let self = this
console.log('getInvitationCardDetail')
console.log(self.invitationCardImg)
self.invitationCard = self.invitationCardImg
let curWidth = self.getViewportSize().width
let curHeight = self.getViewportSize().height
self.invitationCard = !!self.invitationCard ? self.getImageView(3, curWidth, curHeight, self.invitationCard) : ''
console.log(self.invitationCard)
}
}
}



